import { useMemo, useState } from 'react';
import { ButtonCancel, ButtonPrimary } from '../../../../../utils/Button';
import InputField, { InputPassword } from '../../../../../utils/InputField';

function TypeMsSqlHost({ setOpen, type, data, onSave }) {
    const [formData, setFormData] = useState({
        ...(data || {}),
        type: type || data?.type,
        host: data?.host || 'localhost',
        port: data?.port || 1433,
        databaseOrPath: data?.databaseOrPath || '',
        user: data?.user || '',
        password: data?.password || '',
        operation: data?.operation || 'exec' || ''
    });

    // const { configsData } = useConfig();
    // const msSqlDumpPath = configsData?.dumpMssql || '';

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prev) => ({
            ...prev,
            [name]: value
        }));
    };

    // payload to be sent to the server
    const payload = useMemo(() => {
        return {
            ...formData,
            port: parseInt(formData.port, 10)
        };
    }, [formData]);

    const isDisabled =
        !formData.host ||
        !formData.port ||
        !formData.user ||
        !formData.password ||
        !formData.databaseOrPath;

    return (
        <form
            onSubmit={(ev) => {
                onSave(ev, payload);
            }}
        >
            <div className="space-y-2 mb-4">
                <div className="flex items-center gap-x-2">
                    <InputField
                        name="host"
                        label="Host"
                        formData={formData}
                        onChange={handleChange}
                    />
                    <InputField
                        name="port"
                        label="Port"
                        formData={formData}
                        onChange={handleChange}
                    />
                </div>
                <div className="flex items-center gap-x-2">
                    <InputField
                        name="user"
                        label="Username"
                        formData={formData}
                        onChange={handleChange}
                    />
                    <InputPassword
                        name="password"
                        label="Password"
                        formData={formData}
                        onChange={handleChange}
                    />
                </div>
                <InputField
                    name="databaseOrPath"
                    label="Database"
                    formData={formData}
                    onChange={handleChange}
                />
                {/* <SelectOption
                    name="operation"
                    label="Operation Type"
                    formData={formData}
                    onChange={handleChange}
                    options={options?.map((item) => addOption(item.value, item.label, item.value))}
                    className="w-full !mb-3"
                /> */}
            </div>

            {/* <DumpPathRow type="dumpMssql" path={msSqlDumpPath} /> */}

            <div className="flex justify-end gap-2">
                <ButtonCancel title="Cancel" onClick={() => setOpen(false)} />
                <ButtonPrimary title="Save" btnType="submit" disabled={isDisabled} />
            </div>
        </form>
    );
}

export default TypeMsSqlHost;
