import dayjs from 'dayjs';
import { useEffect, useState } from 'react';
import cn from './cn';

interface CountdownTimerProps {
    show?: boolean;
    nextRun?: number;
}

function CountdownTimer({ show = false, nextRun = 0 }: CountdownTimerProps): JSX.Element {
    const calculateTimeLeft = (): number => {
        if (!nextRun) return 0;
        const difference = dayjs.unix(nextRun).diff(dayjs(), 'second');
        return difference > 0 ? difference : 0;
    };

    const [remainingSeconds, setRemainingSeconds] = useState<number>(calculateTimeLeft());

    useEffect(() => {
        const timer = setInterval(() => {
            setRemainingSeconds((prevSeconds) => Math.max(0, prevSeconds - 1));
        }, 1000);

        return () => clearInterval(timer);
    }, [nextRun]);

    const formatTime = (seconds: number): string => {
        const hours = Math.floor(seconds / 3600);
        const minutes = Math.floor((seconds % 3600) / 60);
        const rmSeconds = seconds % 60;

        return `${hours}h ${minutes}m ${rmSeconds}s`;
    };

    return (
        <span
            className={cn('text-success text-sm', {
                invisible: !show
            })}
            data-tooltip-id="myToolTip"
            data-tooltip-content="Next Backup"
        >
            {formatTime(remainingSeconds)}
        </span>
    );
}

export default CountdownTimer;
