import { ReactNode } from 'react';
import cn from '../../../utils/cn';
import { useConfig } from '../../Section/Config/ConfigProvider';
import UserLoggedInRow from './UserLoggedInRow';
import UserNotLoggedInRow from './UserNotLoggedInRow';

interface ContainerProps {
    children: ReactNode;
    className?: string;
}

function Container({ children, className }: ContainerProps): JSX.Element {
    return (
        <div
            className={cn('absolute bottom-2.5 w-full px-[15px] py-2.5 overflow-hidden', className)}
        >
            {children}
        </div>
    );
}

function UserRow(): JSX.Element {
    const { appId } = useConfig();
    const auth = false;

    if (!auth) {
        return (
            <Container>
                <UserNotLoggedInRow appId={appId} />
            </Container>
        );
    }

    return (
        <Container>
            <UserLoggedInRow appId={appId} />
        </Container>
    );
}

export default UserRow;
