import { MenuItem, useApp } from '../../../context/AppProvider';
import { useConfig } from '../../Section/Config/ConfigProvider';
import { menuList } from '../menuList';
import Menu from './Menu';

function MenuList(): JSX.Element {
    const { handleSelectMenu, selectedMenu } = useApp();
    const { handleReload } = useConfig();

    const onMenuClick = (menu: MenuItem): void => {
        handleSelectMenu(menu);
        handleReload();
    };

    return (
        <div className="h-[calc(100%_-_84px_-_165px)] p-[5px] space-y-1">
            {menuList?.map((menu) => (
                <Menu
                    key={menu.id}
                    icon={menu.icon}
                    title={menu.title}
                    isActive={menu.id === selectedMenu?.id}
                    isRedirect={menu.redirect || false}
                    onMenuClick={() => onMenuClick(menu)}
                    className=""
                />
            ))}
            {/* <div className="w-full flex justify-center">
                <button
                    type="button"
                    className="border border-primary hover:border-secondary-700 rounded-full whitespace-nowrap text-sm text-primary hover:text-secondary-700 px-3 !mt-4"
                    onClick={() => openModal('featureInfo')}
                >
                    Need to add more feature?
                </button>
            </div> */}
        </div>
    );
}

export default MenuList;
