/* eslint-disable no-underscore-dangle */
import { ReactNode } from 'react';
import { EditIcon, FileIcon } from '../../../../utils/SVGIcon';
import cn from '../../../../utils/cn';
import exploreDirectory from '../../../../utils/exploreDirectory';

interface Col2Props {
    title: string;
    value: string | ReactNode;
}

function Col2({ title, value }: Col2Props) {
    return (
        <div className="flex justify-between items-center">
            <div>
                <h2 className="text-sm text-primary font-medium capitalize">
                    <span>{title}</span>
                </h2>
                <p className="text-xs text-primary-700">
                    <span>{value || '[Not Set]'}</span>
                </p>
            </div>
        </div>
    );
}

interface ConfigRowProps {
    title: string;
    icon?: ReactNode;
    value: string | ReactNode;
    openModal: () => void;
    showModify?: boolean;
    explore?: boolean;
    className?: string;
}

function ConfigRow({
    title,
    icon,
    value,
    openModal,
    showModify = false,
    explore = true,
    className
}: ConfigRowProps) {
    return (
        <div className={cn('list_item', className)}>
            <div className="rounded-10 bg-[#FFF3D6]">{icon || <FileIcon className="" />}</div>

            <div>
                <Col2 title={title} value={value} />
            </div>

            <div className="items-center gap-x-4 flex">
                {showModify === true ? (
                    <>
                        <div>
                            {value ? (
                                <button
                                    type="button"
                                    className="bg-secondary-100 hover:bg-secondary rounded-5 px-2 py-1 text-sm text-secondary hover:text-white"
                                    onClick={() => exploreDirectory(value)}
                                    hidden={!explore}
                                >
                                    Explore
                                </button>
                            ) : null}
                        </div>
                        <button type="button" onClick={openModal}>
                            <EditIcon className="" />
                        </button>
                    </>
                ) : null}
            </div>
        </div>
    );
}

export default ConfigRow;
