/* eslint-disable no-underscore-dangle */
import { useEffect, useState } from 'react';
import { useApp } from '../../../context/AppProvider';
import { useTemplate } from '../../../context/template/TemplateProvider';
import backupsDemoData from '../../../data/backupsDemoData';
import LibModal from '../../Features/LibModal/LibModal';
import SectionHeader from '../../Features/SectionHeader/SectionHeader';
import { useConfig } from '../Config/ConfigProvider';
import BackupItem from './Components/BackupItem';
import TitleArea from './Components/TitleArea';
import BackupSyncModal from './Modals/BackupSyncModal';
import { BackupData, NavigateData } from './types';

function BackupsSection() {
    const [backupsData, setBackupsData] = useState<BackupData[]>(backupsDemoData);
    const [reloadKey, setReloadKey] = useState<number>(0);

    const { setMessage } = useTemplate();
    const { destinationData } = useConfig();

    const { navigateData, navigate, modalType, closeModal } = useApp();
    const { _id: sourceId, databaseOrPath: directory } = navigateData || { _id: '' };

    const [formData, setFormData] = useState<NavigateData | null>(navigateData);

    const reloadWithData = () => {
        setReloadKey((prev) => prev + 1);
        setBackupsData(backupsDemoData);
    };

    const reloadWithoutData = () => {
        setReloadKey((prev) => prev + 1);
    };

    const onChange = (value: NavigateData) => {
        if (value) {
            setFormData(value);

            // cleanup navigate data
            navigate('backups', null);
        }
    };

    // Fetch sources running status
    useEffect(() => {
        // setSourcesData(sourceDemoData);
        window?.electronAPI
            ?.getRecentBackups({ sourceId: formData?._id !== 'all' ? formData?._id : '' })
            .then(({ data: resData } = {}) => {
                if (resData) {
                    // if (sourceId) {
                    //     const filterData = resData.filter((d) => d.sourceId === sourceId);
                    //     setBackupsData(filterData);
                    // } else {
                    //     setBackupsData(resData);
                    // }
                    setBackupsData(resData);
                }
            })
            .catch((err: any) => {
                setBackupsData([]);
                setMessage(err?.toString());
            });
    }, [formData?._id, setMessage, reloadKey]);

    const sortedData = backupsData.sort((a, b) => {
        return b.timeCreated - a.timeCreated; // Change to b.timeCreated if you want descending order
    });

    const arr = sortedData.filter((d) => {
        if (!d.demo && formData?._id && formData?._id !== 'all') {
            return d?.sourceId?.includes(formData?._id);
        }

        return d;
    });

    return (
        <div className="w-full h-full">
            <SectionHeader className={sourceId === formData?._id ? 'h-[140px]' : 'h-21'}>
                <TitleArea
                    data={arr}
                    sourceId={sourceId}
                    directory={directory}
                    handleReload={reloadWithData}
                    formData={formData}
                    onChange={onChange}
                />
            </SectionHeader>

            <div className="w-full h-[calc(100%_-_140px)] overflow-auto p-5 pb-24">
                {arr && arr.length === 0 && (
                    <div className="flex justify-center items-center h-full">
                        <p className="text-primary-500 text-lg">No backups found!</p>
                    </div>
                )}
                {arr?.map((backup) => (
                    <BackupItem
                        key={backup?._id}
                        data={backup}
                        destinationData={destinationData?.[0]}
                        reloadWithData={reloadWithData}
                        reloadWithoutData={reloadWithoutData}
                    />
                ))}
            </div>

            {modalType === 'sync-backup' && (
                <LibModal
                    title="Sync backup with cloud"
                    className="!w-100"
                    setOpen={closeModal}
                    subTitle=""
                >
                    <BackupSyncModal reloadWithData={reloadWithData} closeModal={closeModal} />
                </LibModal>
            )}
        </div>
    );
}

export default BackupsSection;
