import { useState } from 'react';
import { useApp } from '../../../../context/AppProvider';
import DirectoryModal from '../../../Features/Directory/DirectoryModal';
import LibModal from '../../../Features/LibModal/LibModal';
import { useConfig } from '../ConfigProvider';
import ModalDumpPathConfig from './ModalDumpPathConfig';
import ModalExportConfigJson from './ModalExportConfigJson';
import ModalImportCloudConfigJson from './ModalImportCloudConfigJson';
import ModalImportLocalJson from './ModalImportLocalJson';
import ModalInstructionMsSQL from './ModalInstructionMsSQL';
import ModalInstructionMySQL from './ModalInstructionMySQL';
import ModalInstructionPgSQL from './ModalInstructionPgSQL';
import ModalNotificationEmail from './ModalNotificationEmail';
import ModalSMTPConfigSet from './ModalSMTPConfigSet';
import ModalSMTPConfigUpdate from './ModalSMTPConfigTest';
import ModalScanDumpFile from './ModalScanDumpFile';
import ModalTestDumpPath from './ModalTestDumpPath';

function Modals() {
    const {
        loading,
        configsData,
        selectedPath,
        setSelectedPath,
        handleReload,
        handleSaveDirectory,
        handleLocalJsonImport,
        handleCloudConfigJsonImport,
        handleConfigExport,
        handleSetDumpPath
    } = useConfig();
    const { closeModal, modalType, modalData } = useApp();
    const [instModal, setInstModal] = useState('');

    const closeClick = () => {
        closeModal();
        setSelectedPath('');
    };
    return (
        <>
            {modalType === 'add' ? (
                <LibModal
                    title="Set Default Directory"
                    subTitle=""
                    className=""
                    setOpen={closeClick}
                >
                    <DirectoryModal
                        path=""
                        selectedPath={selectedPath}
                        setSelectedPath={setSelectedPath}
                        cancelClick={closeClick}
                        onClick={handleSaveDirectory}
                        loading={false}
                    />
                </LibModal>
            ) : null}

            {modalType === 'default_dir' ? (
                <LibModal
                    title={
                        configsData?.defaultDirectory
                            ? 'Update Default Directory'
                            : 'Set Default Directory'
                    }
                    subTitle=""
                    className="!w-[400px]"
                    setOpen={closeClick}
                >
                    <DirectoryModal
                        path={configsData?.defaultDirectory}
                        selectedPath={selectedPath}
                        setSelectedPath={setSelectedPath}
                        cancelClick={closeClick}
                        onClick={handleSaveDirectory}
                        loading={false}
                    />
                </LibModal>
            ) : null}

            {modalType === 'notif_email' ? (
                <LibModal
                    title="Update Notification Email"
                    subTitle=""
                    className="!w-[400px]"
                    setOpen={closeModal}
                >
                    <ModalNotificationEmail
                        data={configsData}
                        closeModal={closeModal}
                        handleReload={handleReload}
                    />
                </LibModal>
            ) : null}

            {modalType === 'import-local-json' ? (
                <LibModal
                    title="Import Configuration (Local)"
                    subTitle=""
                    className="!w-[400px]"
                    setOpen={closeClick}
                >
                    <ModalImportLocalJson
                        path=""
                        selectedPath={selectedPath}
                        setSelectedPath={setSelectedPath}
                        cancelClick={closeClick}
                        onClick={handleLocalJsonImport}
                        loading={false}
                    />
                </LibModal>
            ) : null}

            {modalType === 'import-cloud-json' ? (
                <LibModal
                    title="Import Configuration"
                    subTitle=""
                    className=""
                    setOpen={closeClick}
                >
                    <ModalImportCloudConfigJson
                        path=""
                        loading={loading}
                        // path={configsData?.find((item) => item?._id === 'app_key')?.value}
                        selectedPath={selectedPath}
                        setSelectedPath={setSelectedPath}
                        cancelClick={closeClick}
                        onClick={handleCloudConfigJsonImport}
                    />
                </LibModal>
            ) : null}

            {modalType === 'export-config-json' ? (
                <LibModal
                    title="Export Configuration"
                    subTitle=""
                    className=""
                    setOpen={closeClick}
                >
                    <ModalExportConfigJson
                        path=""
                        selectedPath={selectedPath}
                        staticPath={
                            selectedPath?.length
                                ? ''
                                : // ? `${configsData?.find((item) => item?._id === 'app_key')?.value}-config-exported.json`
                                  ''
                        }
                        setSelectedPath={setSelectedPath}
                        cancelClick={closeClick}
                        onClick={handleConfigExport}
                        loading={loading}
                    />
                </LibModal>
            ) : null}

            {modalType === 'set-smtp-config' ? (
                <LibModal title="SMTP Configuration" subTitle="" className="" setOpen={closeClick}>
                    <ModalSMTPConfigSet
                        closeClick={closeClick}
                        data={modalData}
                        handleReload={handleReload}
                    />
                </LibModal>
            ) : null}

            {modalType === 'test-smtp-config' ? (
                <LibModal
                    title="Test SMTP Configuration"
                    subTitle=""
                    className="!w-[400px]"
                    setOpen={closeClick}
                >
                    <ModalSMTPConfigUpdate closeClick={closeClick} data={modalData} />
                </LibModal>
            ) : null}

            {modalType === 'scan-file' ? (
                <LibModal title="" subTitle="" className="" setOpen={closeClick}>
                    <ModalScanDumpFile
                        closeModal={closeClick}
                        data={modalData}
                        handleSetDumpPath={handleSetDumpPath}
                    />
                </LibModal>
            ) : null}

            {modalType === 'dump-path' ? (
                <LibModal title="Set Dump Path" subTitle="" className="" setOpen={closeModal}>
                    <ModalDumpPathConfig
                        data={configsData}
                        type={modalData}
                        closeModal={closeModal}
                        handleSetDumpPath={handleSetDumpPath}
                        loadingOnSave={loading}
                    />
                </LibModal>
            ) : null}

            {modalType === 'test-dump-path' ? (
                <div className="[&>.lib-modal]:!p-0">
                    <LibModal title="" subTitle="" className="" setOpen={closeModal}>
                        <ModalTestDumpPath
                            data={modalData}
                            openModal={setInstModal}
                            closeModal={closeModal}
                        />{' '}
                    </LibModal>
                </div>
            ) : null}

            {instModal === 'modal_mysql_instruction' ? (
                <LibModal
                    title="Mysql Environment Setup"
                    subTitle=""
                    className=""
                    setOpen={() => {
                        setInstModal('');
                    }}
                >
                    <ModalInstructionMySQL />
                </LibModal>
            ) : null}

            {instModal === 'modal_mssql_instruction' ? (
                <LibModal
                    title="Mssql Environment Setup"
                    subTitle=""
                    className=""
                    setOpen={() => {
                        setInstModal('');
                    }}
                >
                    <ModalInstructionMsSQL />
                </LibModal>
            ) : null}

            {instModal === 'modal_pgsql_instruction' ? (
                <LibModal
                    title="Pgsql Environment Setup"
                    subTitle=""
                    className=""
                    setOpen={() => {
                        setInstModal('');
                    }}
                >
                    <ModalInstructionPgSQL />
                </LibModal>
            ) : null}
        </>
    );
}

export default Modals;
