/* eslint-disable no-underscore-dangle */
import { useState } from 'react';
import { useApp } from '../../../../context/AppProvider';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { OptionButton } from '../../../../utils/Button';
import InstOption from '../../../../utils/InstOption';
import { ReloadIcon } from '../../../../utils/SVGIcon';
import { useConfig } from '../ConfigProvider';

function TitleArea(): JSX.Element {
    const { openModal } = useApp();
    const [showMenu, setShowMenu] = useState<boolean>(false);

    const { configsData, handleReload } = useConfig();
    const { setMessage, setConfirm, setTemplateLoading } = useTemplate();

    // find app config
    const appId = configsData?.appKey;

    const handleReset = (ev: React.MouseEvent): void => {
        ev.stopPropagation();
        setConfirm({
            text: 'Are you sure you want to reset the app?',
            textAction: 'Yes',
            clickAction: () => {
                // -- loading status true on confirm button
                setTemplateLoading(true);
                window.electronAPI
                    ?.resetApp({
                        appId
                    })
                    .then(({ error, message }: { error: number; message?: string }) => {
                        // -- Set message
                        setMessage(message || '');

                        // -- loading status false on confirm button
                        setTemplateLoading(false);

                        if (error === 0) {
                            setConfirm(null);
                            handleReload();
                        }
                    })
                    .catch((err: any) => {
                        // -- Set error message
                        setMessage(err?.toString());

                        // -- loading status false on confirm button
                        setTemplateLoading(false);
                    });
            }
        });
    };

    return (
        <div className="w-full h-full flex items-center justify-between">
            <div className="">
                <p className="text-[25px] text-primary font-medium leading-[30px]">
                    Configurations
                </p>
            </div>
            <div className="flex items-center gap-x-2">
                <button type="button" className="block h-9" onClick={handleReload}>
                    <ReloadIcon className="size-9" />
                </button>
                <div className="relative">
                    <OptionButton className="size-9" onClick={() => setShowMenu(true)} />
                    <InstOption
                        showMenu={showMenu}
                        setShowMenu={setShowMenu}
                        className="top-[53px] -right-[10px]"
                    >
                        <button type="button" onClick={() => openModal('import-local-json')}>
                            Import (Local)
                        </button>
                        <button type="button" onClick={() => openModal('import-cloud-json')}>
                            Import (Cloud)
                        </button>
                        <button type="button" onClick={() => openModal('export-config-json')}>
                            Export
                        </button>
                        <button type="button" onClick={handleReset}>
                            Reset
                        </button>
                    </InstOption>
                </div>
            </div>
            {/* 
            {!defaultDir ? (
                <ButtonPrimary title="Add Default Directory" onClick={() => openModal('add')} />
            ) : null} */}
        </div>
    );
}

export default TitleArea;
