import { PauseIcon, PlayIcon, RefreshIcon } from '../../../../utils/SVGIcon';
import { GetDate } from '../../../../utils/date';

interface BackupData {
    id: number;
    name: string;
    timeCreated: number;
    running: boolean;
}

interface BackupListItemProps {
    data: BackupData;
    handleBackup: (data: BackupData) => void;
}

function BackupListItem({ data, handleBackup }: BackupListItemProps): JSX.Element {
    const { name, timeCreated, running } = data || {};

    return (
        <div className="list_item pr-1">
            <div>
                <RefreshIcon className="bg-secondary-100" />
            </div>
            <div>
                <h2 className="text-sm text-primary font-medium capitalize">
                    <span>{name}</span>
                </h2>
                <p className="text-xs text-primary-700">
                    <span>{GetDate(timeCreated)}</span>
                </p>
            </div>
            <div>
                <button type="button" onClick={() => handleBackup(data)}>
                    {running ? <PauseIcon className="" /> : <PlayIcon className="" />}
                </button>
            </div>
        </div>
    );
}

export default BackupListItem;
