import dayjs from 'dayjs';

export function GetDate(timeStamp: number): string | null {
    if (!timeStamp) {
        return null;
    }
    const dateFormat = dayjs(timeStamp * 1000).format(`DD ${`MMMM`.slice(0, 3)} YYYY`);
    return dateFormat.toString() !== 'Invalid Date' ? dateFormat : '--';
}

export function GetTime(timeStamp: number): string | null {
    if (!timeStamp) {
        return null;
    }
    const dateFormat = dayjs(timeStamp * 1000).format('hh:mm:ss A');
    return dateFormat.toString() || null;
}
