/* eslint-disable no-underscore-dangle */
/* eslint-disable no-unused-vars */
import { useState } from 'react';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { CloudIcon, DownloadIcon, FileIcon, RemoveIcon } from '../../../../utils/SVGIcon';
import cn from '../../../../utils/cn';
import { GetDate, GetTime } from '../../../../utils/date';
import formatBytes from '../../../../utils/formatBytes';
import { BackupData, DestinationData } from '../types';

interface BackupItemProps {
    data: BackupData;
    destinationData?: DestinationData;
    reloadWithData: () => void;
    reloadWithoutData: () => void;
}

function BackupItem({ data, destinationData, reloadWithData, reloadWithoutData }: BackupItemProps) {
    const [loading, setLoading] = useState<boolean>(false);
    // const [selectedPath, setSelectedPath] = useState('');

    const { setMessage, setConfirm, setTemplateLoading } = useTemplate();

    const isDirectory = data?.name?.split(' ')[0].includes('directory');

    const handleDownload = () => {
        window.electronAPI
            ?.openDirectoryDialog()
            .then((res: string) => {
                if (res) {
                    setLoading(true);
                    window.electronAPI
                        ?.downloadBackup({
                            sourceId: data?.sourceId,
                            backupId: data?._id,
                            downloadPath: res
                        })
                        .then(({ error, message }) => {
                            // -- Set success message
                            setMessage(message || 'Download completed');

                            // -- loading false if success
                            setLoading(false);

                            if (error === 0) {
                                // -- Set success message
                                setMessage({
                                    text: message || 'Download completed',
                                    directory: res,
                                    timeout: 20000
                                });

                                setLoading(false);

                                // -- Reload sources data
                                reloadWithoutData();
                            }
                        })
                        .catch((err: any) => {
                            // -- loading false if error
                            setLoading(false);

                            // -- Set error message
                            setMessage(err?.toString());
                        });
                }
            })
            .catch((err: any) => {
                console.log(err);
            });
    };

    const handelRemoveBackup = () => {
        setConfirm({
            text: `Are you sure you want to remove this backup?`,
            textAction: 'Yes',
            clickAction: () => {
                // -- Conifirm button loading true
                setTemplateLoading(true);

                window.electronAPI
                    ?.removeBackup({
                        backupId: data?._id
                    })
                    .then(({ error, message }) => {
                        setMessage(message || 'Backup removed');

                        // -- Conifirm button loading false
                        setTemplateLoading(false);

                        if (error === 0) {
                            setConfirm(null);
                            reloadWithData();
                        }
                    })
                    .catch((err: any) => {
                        setMessage(err?.toString());

                        // -- Conifirm button loading false
                        setTemplateLoading(false);
                    });
            }
        });
    };

    return (
        <div
            className={cn(`list_item`, {
                'placeholder-mode': data?.demo
            })}
        >
            <div
                className={cn({
                    file: isDirectory,
                    cloud: !isDirectory
                })}
            >
                {isDirectory ? <FileIcon className="" /> : <CloudIcon className="" />}
            </div>
            <div>
                <div className="text-sm text-primary font-medium capitalize flex items-center gap-x-1">
                    <span>{GetDate(data?.timeCreated)}</span>
                    <span>&bull;</span>
                    <span>{GetTime(data?.timeCreated)}</span>
                </div>
                <div className="text-xs text-primary-700 ">
                    <span>
                        Size:{' '}
                        {formatBytes(
                            typeof data?.size === 'string'
                                ? parseInt(data.size, 10) || 0
                                : data?.size
                        )}
                    </span>
                </div>
            </div>
            <div className="flex gap-x-2">
                <button
                    type="button"
                    onClick={handelRemoveBackup}
                    className="group bg-[#FFDFDF] hover:bg-error flex items-center justify-center w-11 h-11 !rounded-10 p-[8px]"
                >
                    <RemoveIcon className="text-error group-hover:text-white rounded-10" />
                </button>
                <button
                    type="button"
                    onClick={!loading ? handleDownload : undefined}
                    className="!rounded-10 bg-secondary flex items-center justify-center w-11 h-11 p-[8px]"
                >
                    <DownloadIcon className="rounded-10" animate={loading} />
                </button>
            </div>
        </div>
    );
}

export default BackupItem;
