import { useApp } from '../../../../context/AppProvider';
import { ButtonPrimary } from '../../../../utils/Button';
import cn from '../../../../utils/cn';
import { useConfig } from '../../Config/ConfigProvider';
import { useSource } from '../SourceProvider';

function DestinationItem({ extData, data, handleLinkDestination }) {
    const payload = {
        ...extData,
        destination: data?.keyFilename
    };

    return (
        <li
            className={cn(`flex justify-between items-center bg-secondary-100 p-2 rounded-10`, {
                'placeholder-mode': data?.demo
            })}
        >
            <div>
                <h2 className="text-primary">
                    {' '}
                    <span>{data?.title}</span>
                </h2>
                <h2 className="text-primary-700 text-xs">
                    <span>{data?.type}</span>
                </h2>
            </div>

            <div>
                <ButtonPrimary title="Link" onClick={() => handleLinkDestination(payload)} />
            </div>
        </li>
    );
}

function DestinationLinkingModal({ data }) {
    const { destinationData } = useConfig();

    const { closeModal, handleReload } = useSource();

    const { setMessage } = useApp();

    // --Handler to link destination
    const handleLinkDestination = () => {
        window.electronAPI
            .linkDestination(data)
            .then(({ error, message }) => {
                // -- Set message
                setMessage(message);

                if (error === 0) {
                    // -- Close modal
                    closeModal();

                    // -- Reload sources data
                    handleReload();
                }
            })
            .catch((err) => {
                // -- Set error message
                setMessage(err?.toString());
                console.log(err);
            });
    };

    return (
        <div>
            <ul>
                {destinationData && !destinationData?.length ? (
                    <div className="w-full h-full flex justify-center items-center p-4">
                        <p className="text-lg text-gray-400">No destinations found</p>
                    </div>
                ) : null}

                {destinationData?.map((item) => (
                    <DestinationItem
                        key={item?.projectId}
                        data={item}
                        extData={data}
                        handleLinkDestination={handleLinkDestination}
                    />
                ))}
            </ul>
        </div>
    );
}

export default DestinationLinkingModal;
