/* eslint-disable no-underscore-dangle */
import { useEffect, useState } from 'react';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import backupsDemoData from '../../../../data/backupsDemoData';
import { CloudIcon, DownloadIcon, FileIcon } from '../../../../utils/SVGIcon';
import cn from '../../../../utils/cn';
import { GetDate, GetTime } from '../../../../utils/date';
import truncateString from '../../../../utils/truncateString';
import { useConfig } from '../../Config/ConfigProvider';

function BackupItem({ data }) {
    const isDirectory = data?.name?.split(' ')[0].includes('directory');
    return (
        <div
            className={cn(`list_item pr-1`, {
                'placeholder-mode': data?.demo
            })}
        >
            <div
                className={cn({
                    file: isDirectory,
                    cloud: !isDirectory
                })}
            >
                {isDirectory ? <FileIcon /> : <CloudIcon />}
            </div>
            <div>
                <h2 className="text-sm text-primary font-medium capitalize">
                    <span>{truncateString(data?.name)}</span>
                </h2>
                <div className="text-xs text-primary-700 flex items-center gap-x-1">
                    <span>{GetDate(data?.timeCreated)}</span>
                    <span>&bull;</span>
                    <span>{GetTime(data?.timeCreated)}</span>
                </div>
            </div>
            <div>
                <button type="button" onClick={null}>
                    <DownloadIcon />
                </button>
            </div>
        </div>
    );
}

function SourceBackupsModal({ data }) {
    const [backupsData, setBackupsData] = useState(backupsDemoData);

    const { setMessage } = useTemplate();
    const { destinationData } = useConfig();

    // Fetch sources running status
    useEffect(() => {
        // setSourcesData(sourceDemoData);
        window?.electronAPI
            ?.getRecentBackups(destinationData?.[0])
            .then(({ data: resData }) => {
                if (resData) {
                    const filterData = resData.filter((d) => d.sourceId === data?._id);

                    setBackupsData(filterData);
                }
            })
            .catch((err) => {
                setBackupsData([]);
                setMessage(err?.toString());
                console.log(err);
            });
    }, [data?._id, destinationData, setMessage]);

    return (
        <div className="w-full h-full pb-6">
            <div className="flex items-center gap-x-2 mb-3 font-medium h-13">
                <span className="text-primary-700">Source ID:</span>{' '}
                <span className="text-secondary text-sm"> {data?._id}</span>
            </div>
            <div className="h-[calc(100%_-_52px)] overflow-auto border border-secondary-100 rounded-10 px-2">
                {backupsData && backupsData.length === 0 && (
                    <div className="flex items-center justify-center h-full py-6">
                        <span className="text-primary-700">No backups found!</span>
                    </div>
                )}
                {backupsData?.map((backup) => (
                    <BackupItem key={backup._id} data={backup} />
                ))}
            </div>
        </div>
    );
}

export default SourceBackupsModal;
