import { useEffect, useRef, useState } from 'react';
import cn from './cn';

interface ArrowIconProps {
    isOpen: boolean;
}

function ArrowIcon({ isOpen }: ArrowIconProps): JSX.Element {
    return (
        <svg
            className={cn(`feather feather-chevron-up w-4 h-4 transition-transform duration-300`, {
                'transform rotate-180': isOpen
            })}
            xmlns="http://www.w3.org/2000/svg"
            width="100%"
            height="100%"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
        >
            <polyline points="18 15 12 9 6 15" />
        </svg>
    );
}

interface OptionItem {
    id: string | number;
    title: string;
    value: string | number;
}

interface OptionProps {
    option: OptionItem;
    handleSelect: (value: string | number) => void;
    isSelected: boolean;
}

function Option({ option, handleSelect, isSelected }: OptionProps): JSX.Element {
    return (
        <div
            role="button"
            tabIndex={-1}
            onClick={() => handleSelect(option?.value)}
            className="cursor-pointer w-full border-gray-100 border-b hover:bg-teal-100"
        >
            <div
                className={cn(
                    `flex w-full items-center p-2 pl-2 border-transparent bg-white border-l-2 relative hover:bg-secondary-100 hover:text-secondary border-secbg-secondary-100`,
                    {
                        'bg-secondary-100 text-secondary': isSelected
                    }
                )}
            >
                <div className="w-full items-center flex overflow-hidden">
                    <div className="mx-2 leading-6 overflow-hidden text-sm text-ellipsis">
                        {option?.title}
                    </div>
                </div>
            </div>
        </div>
    );
}

interface OptionsProps {
    isOpen: boolean;
    options: OptionItem[];
    handleSelect: (value: string | number) => void;
    selectedOption: string | number;
}

function Options({ isOpen, options, handleSelect, selectedOption }: OptionsProps): JSX.Element {
    return (
        <div
            className={cn(
                `absolute shadow w-full left-0 rounded max-h-select overflow-y-auto transition-all svelte-5uyqqj`,
                {
                    'top-13 opacity-100 visible z-40': isOpen,
                    'top-0 opacity-0 invisible -z-40': !isOpen
                }
            )}
        >
            <div className="flex flex-col w-full">
                {options?.map((option) => (
                    <Option
                        key={option?.id}
                        option={option}
                        isSelected={option.value === selectedOption}
                        handleSelect={handleSelect}
                    />
                ))}
            </div>
        </div>
    );
}

interface FormData {
    [key: string]: any;
}

interface SelectOptionProps {
    name: string;
    label: string;
    options: OptionItem[];
    onSelect?: (name: string, value: string | number) => void;
    onChange?: (e: React.ChangeEvent<HTMLSelectElement>) => void;
    formData: FormData;
    placeholder?: string;
}

function SelectOption({
    name,
    label,
    options,
    onSelect,
    onChange,
    formData,
    placeholder
}: SelectOptionProps): JSX.Element {
    const [isOpen, setIsOpen] = useState<boolean>(false);
    const [selectedOption, setSelectedOption] = useState<string | number>(formData[name] || '');

    const ref = useRef<HTMLDivElement>(null);

    const toggleDropdown = (): void => {
        setIsOpen(!isOpen);
    };

    const handleSelect = (value: string | number): void => {
        setSelectedOption(value);
        if (onSelect) {
            onSelect(name, value);
        }
        if (onChange) {
            // Create a synthetic event for compatibility
            const syntheticEvent = {
                target: { name, value }
            } as React.ChangeEvent<HTMLSelectElement>;
            onChange(syntheticEvent);
        }
        setIsOpen(false);
    };

    useEffect(() => {
        const handleClickOutside = (event: MouseEvent) => {
            if (ref.current && !ref.current.contains(event.target as Node)) {
                setIsOpen(false);
            }
        };
        document.addEventListener('mousedown', handleClickOutside);
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, []);

    useEffect(() => {
        setSelectedOption(formData[name] || '');
    }, [formData, name]);

    const selectedOptionData = options.find((option) => option.value === selectedOption);

    return (
        <div className="flex flex-col items-center relative w-full" ref={ref}>
            <div className="w-full">
                <div className="mb-2">
                    <label className="text-gray-700 text-sm">{label}</label>
                </div>
                <div className="relative">
                    <div
                        className="p-1 flex border border-gray-200 bg-white rounded cursor-pointer"
                        onClick={toggleDropdown}
                        role="button"
                        tabIndex={0}
                        onKeyPress={(e) => {
                            if (e.key === 'Enter' || e.key === ' ') {
                                toggleDropdown();
                            }
                        }}
                    >
                        <div className="flex flex-auto flex-wrap">
                            <div className="flex-1">
                                <input
                                    name={name}
                                    value={selectedOptionData?.title || ''}
                                    readOnly
                                    className="bg-transparent p-1 px-2 appearance-none outline-none h-full w-full text-gray-800"
                                    placeholder={`Select ${label}`}
                                />
                            </div>
                        </div>
                        <div className="text-gray-300 w-8 py-1 pl-2 pr-1 border-l flex items-center border-gray-200 svelte-1l8159u">
                            <button
                                type="button"
                                onClick={toggleDropdown}
                                className="cursor-pointer w-6 h-6 text-gray-600 outline-none focus:outline-none"
                            >
                                <ArrowIcon isOpen={isOpen} />
                            </button>
                        </div>
                    </div>
                </div>
                <Options
                    isOpen={isOpen}
                    options={options}
                    selectedOption={selectedOption}
                    handleSelect={handleSelect}
                />
            </div>
        </div>
    );
}

export default SelectOption;
