/* eslint-disable no-underscore-dangle */
import { useEffect, useRef, useState } from 'react';
import capitalizeFirstLetter from '../../../utils/capitalizeFirstLetter';
import cn from '../../../utils/cn';

// Types for the dropdown option
interface DropdownOption {
    _id: string;
    databaseOrPath?: string;
    type?: string;
}

interface ArrowIconProps {
    isOpen: boolean;
}

function ArrowIcon({ isOpen }: ArrowIconProps): JSX.Element {
    return (
        <svg
            className={cn(`feather feather-chevron-up w-4 h-4 transition-transform duration-300`, {
                'transform rotate-180': isOpen
            })}
            xmlns="http://www.w3.org/2000/svg"
            width="100%"
            height="100%"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
        >
            <polyline points="18 15 12 9 6 15" />
        </svg>
    );
}

interface OptionProps {
    option: DropdownOption;
    handleSelect: (option: DropdownOption) => void;
    isSelected: boolean;
}

function Option({ option, handleSelect, isSelected }: OptionProps): JSX.Element {
    return (
        <div
            role="button"
            tabIndex={-1}
            onClick={() => handleSelect(option)}
            className="cursor-pointer w-full border-gray-100 border-b hover:bg-teal-100"
        >
            <div
                className={cn(
                    `flex w-full items-center p-2 pl-2 border-transparent bg-white border-l-2 relative hover:bg-secondary-100 hover:text-secondary border-secbg-secondary-100`,
                    {
                        'bg-secondary-100 text-secondary': isSelected
                    }
                )}
            >
                <div className="w-full items-center flex overflow-hidden">
                    <div className="mx-1 leading-6 overflow-hidden text-sm text-ellipsis">
                        <span className="text-secondary text-sm">
                            {capitalizeFirstLetter(option?.type || '')}
                        </span>
                        {option._id === 'all' ? (
                            <span />
                        ) : (
                            <span className="block whitespace-nowrap overflow-hidden text-ellipsis text-xs text-primary-700">
                                {option?.databaseOrPath}
                            </span>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
}

interface OptionsProps {
    isOpen: boolean;
    options: DropdownOption[];
    handleSelect: (option: DropdownOption) => void;
    selectedOption: DropdownOption;
}

function Options({ isOpen, options, handleSelect, selectedOption }: OptionsProps): JSX.Element {
    return (
        <div
            className={cn(
                `absolute shadow w-full left-0 rounded max-h-select overflow-y-auto transition-all svelte-5uyqqj`,
                {
                    'top-13 opacity-100 visible z-40': isOpen,
                    'top-0 opacity-0 invisible -z-40': !isOpen
                }
            )}
        >
            <div className="flex flex-col w-full">
                {/* <DefaultOption label={label} handleSelect={handleSelect} /> */}
                {options?.map((option) => (
                    <Option
                        key={option?._id}
                        option={option}
                        isSelected={option._id === selectedOption?._id}
                        handleSelect={handleSelect}
                    />
                ))}
            </div>
        </div>
    );
}

interface SourceDropdownProps {
    label: string;
    options: DropdownOption[];
    onSelect: (option: DropdownOption) => void;
    formData?: DropdownOption | null;
}

function SourceDropdown({ label, options, onSelect, formData }: SourceDropdownProps): JSX.Element {
    const [isOpen, setIsOpen] = useState<boolean>(false);
    const [selectedOption, setSelectedOption] = useState<DropdownOption>({} as DropdownOption);

    const ref = useRef<HTMLDivElement>(null);

    // on outside click close dropdown
    useEffect(() => {
        const handleClickOutside = (event: MouseEvent): void => {
            if (ref.current && !ref.current.contains(event.target as Node)) {
                setIsOpen(false);
            }
        };

        document.addEventListener('mousedown', handleClickOutside);
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, [ref]);

    const toggleDropdown = (): void => {
        setIsOpen(!isOpen);
    };

    const handleSelect = (option: DropdownOption): void => {
        setSelectedOption(option);
        onSelect(option);
        toggleDropdown();
    };

    return (
        <div
            ref={ref}
            className={cn(`flex-auto flex flex-col items-center transition-all w-full`, {
                'h-10': true
                // 'h-64': isOpen
            })}
        >
            <div className="flex flex-col items-center relative w-full">
                <div className="w-full svelte-1l8159u">
                    <div className=" bg-white p-1 px-3 pr-0 flex border border-gray-200 rounded svelte-1l8159u overflow-hidden">
                        <div className="flex flex-auto flex-wrap" />

                        <button
                            type="button"
                            onClick={toggleDropdown}
                            className="appearance-none outline-none w-full text-gray-800 text-sm text-left text-ellipsis svelte-1l8159u cursor-default overflow-hidden whitespace-nowrap"
                        >
                            {/* {selectedOption?._id || label} */}
                            <span className="text-secondary text-sm">
                                {capitalizeFirstLetter(
                                    selectedOption?.type || formData?.type || ''
                                ) || label}
                            </span>
                            {selectedOption._id === 'all' ? (
                                <span />
                            ) : (
                                <span className="block whitespace-nowrap overflow-hidden text-ellipsis text-xs text-primary-700">
                                    {selectedOption?.databaseOrPath || formData?.databaseOrPath}
                                </span>
                            )}
                        </button>

                        <div className="text-gray-300 w-8 py-1 pl-2 pr-1 border-l flex items-center border-gray-200 svelte-1l8159u">
                            <button
                                type="button"
                                onClick={toggleDropdown}
                                className="cursor-pointer w-6 h-6 text-gray-600 outline-none focus:outline-none"
                            >
                                <ArrowIcon isOpen={isOpen} />
                            </button>
                        </div>
                    </div>
                </div>
                <Options
                    isOpen={isOpen}
                    options={options}
                    selectedOption={selectedOption}
                    handleSelect={handleSelect}
                />
            </div>
        </div>
    );
}

export default SourceDropdown;
