/* eslint-disable no-underscore-dangle */
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { UpArrowIcon } from '../../../../utils/SVGIcon';
import cn from '../../../../utils/cn';
import copyToClipboard from '../../../../utils/copyToClipboard';

function InfoRow({ title, value, width = '145px', className }) {
    return (
        <div className="flex gap-x-2 text-sm leading-[25px]">
            <div className="text-primary-700 text-nowrap" style={{ width }}>
                {title}:
            </div>
            <div
                className={cn(`text-primary text-left overflow-hidden text-ellipsis`, className)}
                style={{
                    width: `calc(100% - ${width})`
                }}
            >
                {value}
            </div>
        </div>
    );
}

function AutoStart({ enabled = false, onClick }) {
    return (
        <div className="flex items-center gap-x-2 text-sm leading-[25px]">
            <div className="text-primary-700 text-nowrap w-20">Auto Start:</div>
            <div className="text-primary text-left overflow-hidden text-ellipsis w-[calc(100%_-_80px)]">
                <button
                    type="button"
                    className={cn(
                        `px-1 rounded-30 w-[30px] h-[15px] flex justify-end items-center`,
                        {
                            'bg-success justify-end': enabled,
                            'bg-slate-400 justify-start': !enabled
                        }
                    )}
                    onClick={onClick}
                >
                    <span
                        className={cn(`text-primary rounded-full bg-white w-[9px] h-[9px] `, {})}
                    />
                </button>
            </div>
        </div>
    );
}

function ProcessAction({ handleStart, handleStop, data, isRunning }) {
    const { setConfirm } = useTemplate();

    const stopClick = (ev) => {
        ev.stopPropagation();

        setConfirm({
            text: `Are you sure you want to stop this backup process?`,
            textAction: 'Yes',
            clickAction: () => {
                ev.stopPropagation();
                handleStop(data?._id);
            }
        });
    };

    return (
        <div className="w-18 flex justify-end ">
            <div className="relative -top-[5px]">
                {isRunning ? (
                    <button
                        type="button"
                        className="text-error text-sm font-medium leading-[30px] underline text-nowrap"
                        onClick={stopClick}
                    >
                        Stop Schedule
                    </button>
                ) : (
                    <button
                        type="button"
                        className="text-success text-sm font-medium leading-[30px] underline text-nowrap"
                        onClick={(ev) => handleStart(ev, data?._id)}
                    >
                        Start Schedule
                    </button>
                )}
            </div>
        </div>
    );
}

function SourceExpandedInfo({
    data,
    task,
    destinationData,
    handleStartBackup,
    handleStopBackup,
    closeClick,
    handleUpdateAutoStart
}) {
    const { host, frequency, backupQuantity, backupRetention } = data || {};

    const { title, type } = destinationData[0] || {};

    // task status
    const isRunning = task?.running;

    // source type directory or not
    const isDirectory = data?.type?.split(' ')[0].includes('directory');

    return (
        <div
            className="expand-content_container relative !cursor-default"
            role="button"
            tabIndex={-1}
            onClick={(ev) => ev.stopPropagation()}
        >
            <div className="flex items-center justify-between mb-2">
                <div>
                    Source ID:{' '}
                    <span
                        className="text-secondary text-sm "
                        role="button"
                        tabIndex={-1}
                        data-tooltip-id="myToolTip"
                        data-tooltip-content="Copy"
                        onClick={(ev) => copyToClipboard(ev, data?._id)}
                    >
                        {data?._id}
                    </span>
                </div>
                <AutoStart
                    enabled={data?.autostart}
                    onClick={(ev) =>
                        handleUpdateAutoStart(ev, { ...(data || {}), autostart: !data?.autostart })
                    }
                />
            </div>
            <div className="flex items-start">
                <div className="w-[calc(100%_-_72px)] flex">
                    <div className="w-1/2" hidden={isDirectory}>
                        <InfoRow title="Host" value={host} />
                        {/* <InfoRow title="Operation" value={operation} /> */}
                        <InfoRow title="Frequency" value={frequency} />
                        <InfoRow
                            title="Pattern"
                            value={<span className="text-secondary">{data?.frequencyPattern}</span>}
                        />
                        <InfoRow title="Backup QTY" value={backupQuantity} />
                        <InfoRow title="Backup Retention" value={backupRetention} />
                    </div>
                    <div className="w-1/2">
                        <div className="text-sm text-primary font-semibold inline-block">
                            Destination:
                        </div>
                        <InfoRow title="Title" value={title} width="80px" />
                        <InfoRow title="Type" value={type} width="80px" />
                        {/* <div className="mt-1">
                            <InfoRow
                                title="Directory"
                                value={truncateString(keyFilename)}
                                width="80px"
                                className="font-normal"
                            /> */}
                        {/* {isRunning && (
                                <CountdownTimer key={task?.timeRemaining} nextRun={task?.nextRun} />
                            )} */}
                        {/* </div> */}
                    </div>
                </div>

                <ProcessAction
                    handleStart={handleStartBackup}
                    handleStop={handleStopBackup}
                    data={data}
                    isRunning={isRunning}
                />
            </div>

            <button
                type="button"
                className="text-error font-medium rounded-full w-5 h-5 flex justify-center items-center absolute bottom-4 right-1"
                data-tooltip-id="myToolTip"
                data-tooltip-content="Collapse"
                onClick={closeClick}
            >
                <UpArrowIcon />
            </button>

            <div className="text-warning mt-2">{data?.errorStatus && data?.errorMessage}</div>
        </div>
    );
}

export default SourceExpandedInfo;
