/* eslint-disable no-underscore-dangle */
import { useApp } from '../../../context/AppProvider';
import LibModal from '../../Features/LibModal/LibModal';
import SectionHeader from '../../Features/SectionHeader/SectionHeader';
import { useConfig } from '../Config/ConfigProvider';
import DestinationItem from './Components/DestinationItem';
import TitleArea from './Components/TitleArea';
import AddGoogleBucketModal from './Modals/AddGoogleBucketModal';

function DestinationSection(): JSX.Element {
    const { destinationData } = useConfig();

    const { modalType, modalData, closeModal } = useApp();

    return (
        <div className="w-full h-full">
            <SectionHeader className="h-24">
                <TitleArea />
            </SectionHeader>

            <div className="w-full h-[calc(100%_-_96px)] overflow-auto p-5 pb-24">
                {destinationData?.map((destination) => (
                    <DestinationItem
                        key={destination?._id}
                        data={destination}
                        handleReload={null}
                    />
                ))}
            </div>

            {modalType === 'cloud_bucket' && (
                <LibModal
                    title="Add Google Bucket"
                    subTitle=""
                    setOpen={closeModal}
                    className="!w-[450px]"
                >
                    <AddGoogleBucketModal closeModal={closeModal} />
                </LibModal>
            )}

            {modalType === 'update_gcloud-bucket' && (
                <LibModal
                    title="Update Google Bucket"
                    subTitle=""
                    setOpen={closeModal}
                    className="!w-[450px]"
                >
                    <AddGoogleBucketModal closeModal={closeModal} data={modalData} />
                </LibModal>
            )}
        </div>
    );
}

export default DestinationSection;
