import { JSX } from 'react';
import AboutUsSection from '../app/Section/AboutUs/AboutUsSection';
import BackupsSection from '../app/Section/Backups/BackupsSection';
import ConfigSection from '../app/Section/Config/ConfigSection';
import ContactSection from '../app/Section/ContactUs/ContactSection';
import DestinationSection from '../app/Section/Destination/DestinationSection';
import FeatureRequestSection from '../app/Section/FeatureRequest/FeatureRequestSection';
import LogSection from '../app/Section/Log/LogSection';
import OverviewSection from '../app/Section/Overview/OverviewSection';
import SourceSection from '../app/Section/Source/SourceSection';
import SupportSection from '../app/Section/Support/SupportSection';

const defaultExport = null;

export function SectionController(section: string): JSX.Element {
    switch (section) {
        case 'overview':
            return <OverviewSection />;
        case 'source':
            return <SourceSection />;
        case 'backups':
            return <BackupsSection />;
        case 'destination':
            return <DestinationSection />;
        case 'config':
            return <ConfigSection />;
        case 'log':
            return <LogSection />;
        case 'aboutUs':
            return <AboutUsSection />;
        case 'feature':
            return <FeatureRequestSection />;
        case 'support':
            return <SupportSection />;
        case 'contactUs':
            return <ContactSection />;
        // case 'login':
        //     return <LoginSection />;
        default:
            return <OverviewSection />;
    }
}

export default defaultExport;
