import { useMemo, useState } from 'react';
import { ButtonCancel, ButtonPrimary } from '../../../../utils/Button';
import InputField, { SelectOption, addOption } from '../../../../utils/InputField';
import Loading from '../../../Features/Loading/Loading';

const options = [
    {
        label: 'Hourly',
        value: 'hourly'
    },
    {
        label: 'Daily',
        value: 'daily'
    }
];

function SourceFrequencyModal({ data, closeModal, onSave, loading }) {
    const [formData, setFormData] = useState({
        ...(data || {}),
        frequency: data?.frequency || '',
        minute: '0',
        hour: '0',
        frequencyPattern: '0 * * * * *',
        backupQuantity: data?.backupQuantity || 0,
        backupRetention: data?.backupRetention || 0
    });

    // const [showField, setShowField] = useState(false);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prev) => {
            // -- if frequency chnages set minute and hour to its default value
            if (name === 'frequency') {
                return {
                    ...prev,
                    minute: '0',
                    hour: '0',
                    [name]: value
                };
            }

            // -- if frequency is hourly then set minute to 0
            if (name === 'minute') {
                return {
                    ...prev,
                    hour: '*',
                    [name]: value
                };
            }

            // -- if frequency is daily then set hour to 0
            if (name === 'hour') {
                return {
                    ...prev,
                    minute: '0',
                    [name]: value
                };
            }
            return {
                ...prev,
                [name]: value
            };
        });
    };

    // -- check if frequency is hourly
    const isHourly = formData.frequency === 'hourly';

    // payload using useMemo
    const payload = useMemo(() => {
        const { minute } = formData;
        const { hour } = formData;

        return {
            ...formData,
            backupQuantity: parseInt(formData.backupQuantity, 10) || 0,
            backupRetention: parseInt(formData.backupRetention, 10) || 0,
            frequencyPattern: `0 ${hour !== '*' && minute === '*' ? '0' : minute} ${hour} * * *`
        };
    }, [formData]);

    // set minute and hour according to pattern
    useMemo(() => {
        const pattern = data?.frequencyPattern?.split(' ');
        if (pattern) {
            setFormData((prev) => ({
                ...prev,
                minute: pattern[1],
                hour: pattern[2]
                // minute: pattern[1] === '*' ? '0' : pattern[1], // if pattern is * then set 0
                // hour: pattern[2] === '*' ? '0' : pattern[2] // if pattern is * then set 0
            }));
        }
    }, [data]);

    return (
        <form
            onSubmit={(ev) => {
                onSave(ev, payload);
            }}
            className="w-full h-full relative"
        >
            <Loading loading={loading} />
            <div className="space-y-2 mb-4">
                <div className="flex gap-x-2">
                    <SelectOption
                        name="frequency"
                        label="Frequency"
                        formData={formData}
                        onChange={handleChange}
                        options={options?.map((item) =>
                            addOption(item.value, item.label, item.value)
                        )}
                        className="w-full"
                    />

                    {isHourly ? (
                        <InputField
                            label="Minute"
                            name="minute"
                            type="number"
                            min="0"
                            max="59"
                            placeholder="Ex: 30"
                            formData={formData}
                            onChange={handleChange}
                        />
                    ) : (
                        <InputField
                            label="Hour"
                            name="hour"
                            type="number"
                            min="0"
                            max="23"
                            placeholder="Ex: 2"
                            formData={formData}
                            onChange={handleChange}
                        />
                    )}
                </div>

                <div className="text-sm text-primary-700 !mb-3">
                    Frequency Pattern:{' '}
                    <span className="text-secondary-700 ml-2">{payload.frequencyPattern}</span>
                </div>
                <InputField
                    name="backupQuantity"
                    label="Number of backups"
                    placeholder="Ex: 5"
                    formData={formData}
                    onChange={handleChange}
                />
                <InputField
                    name="backupRetention"
                    label="Backup Duration (In days)"
                    placeholder="Ex: 30"
                    formData={formData}
                    onChange={handleChange}
                />
            </div>

            <div className="flex justify-end gap-2">
                <ButtonCancel title="Cancel" onClick={closeModal} />
                <ButtonPrimary title="Save" btnType="submit" disabled={loading} />
            </div>
        </form>
    );
}

export default SourceFrequencyModal;
