import electron from '../../../utils/electron';

interface FeatureRequestPayload {
    fullName: string;
    email: string;
    title: string;
    description: string;
}

interface FeatureRequestResponse {
    error: number;
    message?: string;
    data?: any;
}

export const OperationFeatureRequest = (
    payload: FeatureRequestPayload
): Promise<FeatureRequestResponse> =>
    new Promise((resolve, reject) => {
        electron()
            ?.addFeatureRequest(payload)
            .then((data: FeatureRequestResponse) => {
                if (data.error === 0) {
                    resolve(data);
                } else {
                    reject(data.message);
                }
            })
            .catch((err: any) => {
                reject(err);
            });
    });
