import React, { useState } from 'react';
import { ButtonCancel, ButtonPrimary } from '../../../../utils/Button';
import FileUploadArea from '../../../Features/FileUpload/FileUploadArea';

interface ModalDumpPathConfigProps {
    data: Record<string, any>;
    handleSetDumpPath: (type: string, path: string, flag: string) => void;
    type: string;
    closeModal: () => void;
    loadingOnSave: boolean;
}

function ModalDumpPathConfig({
    data,
    handleSetDumpPath,
    type,
    closeModal,
    loadingOnSave
}: ModalDumpPathConfigProps): JSX.Element {
    const [fileDirectory, setFileDirectory] = useState<string>(data?.[type] || '');
    const [loading, setLoading] = useState<boolean>(false);

    const handleSaveClick = (ev: React.MouseEvent<HTMLButtonElement>) => {
        ev.preventDefault();
        ev.stopPropagation();
        handleSetDumpPath(type, fileDirectory, 'true');
    };

    const handleChooseFile = () => {
        setLoading(true);
        window.electronAPI
            ?.openFileDialog()
            .then((res) => {
                setLoading(false);
                if (res) {
                    setFileDirectory(res);
                } else {
                    setFileDirectory((prev) => prev);
                }
            })
            .catch((err) => {
                setLoading(false);
                console.log(err);
            });
    };
    // -- handler to reset selected file
    const handleReset = (ev: React.MouseEvent<HTMLButtonElement>) => {
        ev.preventDefault();
        ev.stopPropagation();
        setFileDirectory('');
    };

    return (
        <div className="space-y-3">
            <FileUploadArea
                handleChooseFile={handleChooseFile}
                handleReset={handleReset}
                choosenFile={fileDirectory}
                loading={loading}
                resetTitle={data?.[type] ? 'Update' : 'Reset'}
                className=""
            />

            <div className="w-full flex justify-end gap-x-2">
                <ButtonCancel title="Cancel" onClick={closeModal} disabled={false}>
                    Cancel
                </ButtonCancel>
                <ButtonPrimary
                    title="Save"
                    onClick={handleSaveClick}
                    loading={loadingOnSave}
                    disabled={false}
                />
            </div>
        </div>
    );
}

export default ModalDumpPathConfig;
