/* eslint-disable react/jsx-props-no-spreading */
/* eslint-disable react/button-has-type */
import React, { ReactNode } from 'react';
import { DeleteIcon, LoadingAnimateIcon, OptionIcon } from './SVGIcon';
import cn from './cn';

const btnCmnStyle =
    'capitalize text-white text-xs md:text-sm font-medium py-px md:py-2 px-2 md:px-3 rounded md:rounded-lg h-6 md:h-10 min-w-[40px] md:min-w-[80px] disabled:bg-primary-100 disabled:text-primary-700 disabled:cursor-default disabled:select-none disabled:pointer-event-none focus:outline-none ';

const btnLCmnStyle =
    'capitalize text-primary hover:text-white transition text-sm font-medium py-2 px-3 border rounded-lg min-w-[80px]';

interface ButtonPrimaryProps {
    title?: string | ReactNode;
    onClick?: ((e: any) => void) | null;
    disabled?: boolean;
    loading?: boolean;
    className?: string;
    btnType?: 'button' | 'submit' | 'reset';
    children?: ReactNode;
}

// Button Primary with Animate
export function ButtonPrimary({
    title,
    onClick,
    disabled,
    loading = false,
    className = '',
    btnType = 'button',
    children
}: ButtonPrimaryProps): JSX.Element {
    return (
        <button
            type={btnType || 'button'}
            onClick={onClick || undefined}
            className={cn(`relative bg-secondary overflow-hidden`, btnCmnStyle, className, {
                'pointer-events-none': loading
            })}
            disabled={disabled}
        >
            {loading && (
                <span
                    className={`inline-block h-full w-full top-0 right-0 absolute bg-secondary ${btnCmnStyle}`}
                >
                    <LoadingAnimateIcon className="" />
                </span>
            )}
            {children || title}
        </button>
    );
}

interface ButtonDangerProps {
    title?: string;
    onClick?: (e: React.MouseEvent<HTMLButtonElement>) => void;
    disabled?: boolean;
    loading?: boolean;
    className?: string;
    btnType?: 'button' | 'submit' | 'reset';
    children?: ReactNode;
}

// Button Danger
export function ButtonDanger({
    title,
    onClick,
    disabled,
    loading = false,
    className = '',
    btnType = 'button',
    children
}: ButtonDangerProps): JSX.Element {
    return (
        <button
            type={btnType}
            onClick={onClick}
            className={`bg-error relative ${btnCmnStyle} ${className} ${
                loading ? 'pointer-events-none' : ''
            }`}
            disabled={disabled}
        >
            {loading && (
                <span
                    className={`inline-block h-full w-full top-0 right-0 absolute bg-error ${btnCmnStyle}`}
                >
                    <LoadingAnimateIcon className="" />
                </span>
            )}
            {children || title}
        </button>
    );
}

interface ButtonLightProps {
    title?: string;
    onClick?: (e: React.MouseEvent<HTMLButtonElement>) => void;
    disabled?: boolean;
    className?: string;
    children?: ReactNode;
}

// Button Light
export function ButtonLight({
    title,
    onClick,
    disabled,
    className = '',
    children
}: ButtonLightProps): JSX.Element {
    return (
        <button
            type="button"
            onClick={onClick}
            className={cn(
                `hover:bg-secondary hover:border-transparent border-secondary text-secondary disabled:pointer-events-none disabled:grayscale disabled:select-none`,
                btnLCmnStyle,
                className
            )}
            disabled={disabled}
        >
            {children || title}
        </button>
    );
}

interface ButtonDeleteProps {
    children?: ReactNode;
    onClick?: (e: React.MouseEvent<HTMLButtonElement>) => void;
    className?: string;
    loading?: boolean;
}

// Button Delete
export function ButtonDelete({
    children,
    onClick,
    className = '',
    loading = false
}: ButtonDeleteProps): JSX.Element {
    if (loading) {
        return (
            <button
                type="button"
                onClick={undefined}
                className={`relative w-7 h-7 bg-error/10 hover:bg-error text-error hover:text-white p-1 rounded transition ${className}`}
            >
                <div className="w-10 h-8 absolute top-1/2 right-[43%] translate-x-1/2 -translate-y-1/2">
                    <LoadingAnimateIcon className="" />
                </div>
                {children || <DeleteIcon className="" />}
            </button>
        );
    }
    return (
        <button
            type="button"
            onClick={onClick}
            className={`w-7 h-7 bg-error/10 hover:bg-error text-error hover:text-white p-1 rounded transition ${className}`}
        >
            {children || <DeleteIcon className="" />}
        </button>
    );
}

interface OptionButtonProps {
    className?: string;
    onClick: () => void;
}

export function OptionButton({ className, onClick }: OptionButtonProps): JSX.Element {
    const handleOptionClick = (e: React.MouseEvent<HTMLButtonElement>) => {
        e.stopPropagation();
        onClick();
    };

    return (
        <button
            type="button"
            className={cn(
                `p-[7px] rounded-10 block bg-[#c84dce36] hover:bg-secondary [&>svg]:text-secondary [&>svg]:hover:text-white transition-colors`,
                className
            )}
            onClick={handleOptionClick}
        >
            <OptionIcon className="" />
        </button>
    );
}

interface ButtonProps {
    children?: ReactNode;
    title?: string;
    onClick?: (e: React.MouseEvent<HTMLButtonElement>) => void;
    disabled?: boolean;
    className?: string;
    type?: 'button' | 'submit' | 'reset';
    loading?: boolean;
}

function Button({
    children,
    title,
    onClick,
    disabled,
    className,
    type = 'button',
    loading = false
}: ButtonProps): JSX.Element {
    if (loading) {
        return (
            <button
                type={type}
                onClick={undefined}
                className={cn('bg-secondary relative', btnCmnStyle, className)}
                disabled={disabled}
            >
                <div className="w-10 h-8 absolute top-1/2 right-[43%] translate-x-1/2 -translate-y-1/2 disabled:grayscale-[0.5] disabled:pointer-events-none disabled:cursor-default disabled:select-none">
                    <LoadingAnimateIcon className="" />
                </div>
                {children || title}
            </button>
        );
    }
    return (
        <button
            type={type}
            onClick={onClick}
            className={cn('bg-secondary h-10', btnCmnStyle, className)}
        >
            {children || title}
        </button>
    );
}

