import cn from '../../../utils/cn';
import truncateString from '../../../utils/truncateString';
import Loading from '../Loading/Loading';

interface FileUploadAreaProps {
    title?: string;
    resetTitle?: string;
    choosenFile?: string;
    loading: boolean;
    handleChooseFile: () => void;
    handleReset?: (ev: any) => void;
    className?: string;
}

function FileUploadArea({
    title = 'Choose a file',
    resetTitle = 'Reset',
    choosenFile,
    loading,
    handleChooseFile,
    handleReset,
    className
}: FileUploadAreaProps): JSX.Element {
    return (
        <button
            type="button"
            className={cn(
                `w-full h-28 flex items-center justify-center bg-secondary-100 border border-secondary-500 border-dashed rounded-10 relative`,
                className,
                {
                    'cursor-default': choosenFile || loading
                }
            )}
            onClick={handleChooseFile}
        >
            {choosenFile ? (
                <>
                    <Loading loading={loading} className="text-secondary" />
                    <div className="text-secondary font-medium text-sm">
                        <div className="mb-1">
                            Choosen File:
                            {handleReset ? (
                                <div
                                    role="button"
                                    tabIndex={-1}
                                    onClick={handleReset}
                                    className="text-secondary hover:text-white text-xs ml-2 border border-secondary-500 hover:border-transparent hover:bg-secondary px-2 rounded-10"
                                >
                                    {resetTitle}
                                </div>
                            ) : null}
                        </div>
                        <span className="text-primary">{truncateString(choosenFile, 15)}</span>
                    </div>
                </>
            ) : (
                <span className="text-secondary font-medium">{title}</span>
            )}
        </button>
    );
}

export default FileUploadArea;
