import { createContext, ReactNode, useContext, useEffect, useMemo, useState } from 'react';
import '../app/types/electron'; // Import electron types

// Define types for the backup item status
interface BackupItemStatus {
    [key: string]: string | number | boolean | any;
}

// Define the context type
interface InitContextType {
    backupItem: BackupItemStatus;
    reloadInit: () => void;
}

const InitContext = createContext<InitContextType | undefined>(undefined);

export const useInit = (): InitContextType => {
    const context = useContext(InitContext);
    if (context === undefined) {
        throw new Error('useInit must be used within an InitProvider');
    }
    return context;
};

interface InitProviderProps {
    children: ReactNode;
}

function InitProvider({ children }: InitProviderProps) {
    const [backupItem, setBackupItem] = useState<BackupItemStatus>({});
    const [reloadKey, setReloadKey] = useState<number>(0);

    useEffect(() => {
        window.electronAPI?.messageOn(
            'task-status',
            (ev: any, { id, status }: { id: string; status: any }) => {
                setBackupItem((prevData) => ({
                    ...prevData,
                    [id]: status
                }));
            }
        );

        return () => {
            window.electronAPI?.messageOff('task-status');
        };
    }, []);

    useEffect(() => {
        window.electronAPI?.messageOn(
            'download-progress',
            (ev: any, { id, status }: { id: string; status: any }) => {
                console.log('Download progress:', id, status);
            }
        );

        return () => {
            window.electronAPI?.messageOff('task-status');
        };
    }, []);

    useEffect(() => {
        window.electronAPI
            ?.init()
            .then(() => {
                // console.log(data);
            })
            .catch((err: any) => {
                console.log(err);
            });
    }, [reloadKey]);

    const value = useMemo((): InitContextType => {
        const reloadInit = (): void => {
            setReloadKey((prev) => prev + 1);
        };

        return {
            backupItem,
            reloadInit
        };
    }, [backupItem]);

    return <InitContext.Provider value={value}>{children}</InitContext.Provider>;
}

export default InitProvider;
