import { IconMsSql, IconMySql, IconPgSql } from '../../../../utils/SVGIcon';
import ConfigDumpItem from './ConfigDumpItem';

interface ConfigTabDumpPathProps {
    data: any;
    openModal: (type: string, data?: any) => void;
}

function ConfigTabDumpPath({ data, openModal }: ConfigTabDumpPathProps): JSX.Element {
    const testClick = (type: string, value: any): void => {
        openModal('test-dump-path', {
            type,
            path: value
        });
    };

    return (
        <>
            <div className="flex items-center mt-4 mb-2">
                <h2 className="whitespace-nowrap">Dump Path Configuration</h2>

                <hr className="w-full" />
            </div>
            <ConfigDumpItem
                title="MySQL"
                type="dumpMysql"
                icon={<IconMySql className="" />}
                value={data?.dumpMysql?.length ? data?.dumpMysql : 'Not Set'}
                chooseFileClick={openModal}
                testClick={testClick}
                className="[&>div:first-child]:bg-[#FDEFDB]"
            />
            <ConfigDumpItem
                title="MsSQL"
                type="dumpMssql"
                icon={<IconMsSql className="" />}
                value={data?.dumpMssql?.length ? data?.dumpMssql : 'Not Set'}
                chooseFileClick={openModal}
                testClick={testClick}
                className="[&>div:first-child]:bg-[#FFEBEB]"
            />
            <ConfigDumpItem
                title="PgSQL"
                type="dumpPgsql"
                icon={<IconPgSql className="" />}
                value={data?.dumpPgsql?.length ? data?.dumpPgsql : 'Not Set'}
                chooseFileClick={openModal}
                testClick={testClick}
                className="[&>div:first-child]:bg-[rgba(51,103,145,0.10)]"
            />
        </>
    );
}

export default ConfigTabDumpPath;
