import electron from '../../../utils/electron';

const defaultExport = null;

export const OperationCreateSource = (payload) =>
    new Promise((resolve, reject) => {
        electron()
            ?.addSource(payload)
            .then((data) => {
                if (data.error === 0) {
                    resolve(data);
                } else {
                    reject(data.message);
                }
            })
            .catch((err) => {
                reject(err);
            });
    });

