import React, { useState } from 'react';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { OptionButton } from '../../../../utils/Button';
import InstOption from '../../../../utils/InstOption';
import { ReloadIcon } from '../../../../utils/SVGIcon';

interface TitleAreaProps {
    reloadWithData: () => void;
}

interface ApiResponse {
    error: number;
    message?: string;
    data?: any;
}

function TitleArea({ reloadWithData }: TitleAreaProps): JSX.Element {
    const [showMenu, setShowMenu] = useState<boolean>(false);
    const { setMessage, setConfirm, setTemplateLoading } = useTemplate();

    const handleDeleteAll = (ev: React.MouseEvent<HTMLButtonElement | HTMLDivElement>): void => {
        ev.stopPropagation();
        setConfirm({
            text: 'Are you sure you want to delete all logs?',
            textAction: 'Yes',
            clickAction: () => {
                setTemplateLoading(true);
                window.electronAPI
                    ?.emptyLogFiles()
                    .then(({ error, message }: ApiResponse) => {
                        setMessage(message || '');
                        setTemplateLoading(false);
                        if (error === 0) {
                            setConfirm(null);
                            reloadWithData();
                        }
                    })
                    .catch((err: any) => {
                        setMessage(err?.toString() || 'An error occurred');
                        setTemplateLoading(false);
                    });
            }
        });
    };

    return (
        <div className="w-full h-full flex items-center justify-between">
            <div className="">
                <p className="text-[25px] text-primary font-medium leading-[30px]">Logs</p>
            </div>
            <div className="flex items-center gap-x-2">
                <button type="button" className="block h-9" onClick={reloadWithData}>
                    <ReloadIcon className="" />
                </button>
                <div className="relative">
                    <OptionButton className="size-9" onClick={() => setShowMenu(true)} />
                    <InstOption
                        showMenu={showMenu}
                        setShowMenu={setShowMenu}
                        className="top-[53px] -right-[10px]"
                    >
                        <button type="button" onClick={handleDeleteAll}>
                            Delete All
                        </button>
                    </InstOption>
                </div>
            </div>
        </div>
    );
}

export default TitleArea;
