import { createContext, ReactNode, useContext, useMemo, useRef, useState } from 'react';
import ConfirmTemplate from './ConfirmTemplate';
import MessageTemplate from './MessageTemplate';

interface MessageType {
    text?: string;
    timeout?: number;
    directory?: string;
    type?: 'success' | 'error' | 'warning' | 'info';
    title?: string;
    content?: string;
    [key: string]: any;
}

interface ConfirmType {
    icon?: string;
    text?: string;
    buttons?: string[];
    showButton?: boolean;
    showIcon?: boolean;
    textCancel?: string;
    textAction?: string;
    clickAction?: (confirm: ConfirmType, setConfirm: (confirm: ConfirmType | null) => void) => void;
    title?: string;
    content?: string;
    onConfirm?: () => void;
    onCancel?: () => void;
    [key: string]: any;
}

interface TemplateContextType {
    suggestRef: React.RefObject<HTMLDivElement | null>;
    message: MessageType | string | null;
    setMessage: React.Dispatch<React.SetStateAction<MessageType | string | null>>;
    confirm: ConfirmType | null;
    setConfirm: React.Dispatch<React.SetStateAction<ConfirmType | null>>;
    templateLoading: boolean;
    setTemplateLoading: React.Dispatch<React.SetStateAction<boolean>>;
}

const TemplateContext = createContext<TemplateContextType | undefined>(undefined);

export function useTemplate(): TemplateContextType {
    const context = useContext(TemplateContext);
    if (context === undefined) {
        throw new Error('useTemplate must be used within a TemplateProvider');
    }
    return context;
}

interface TemplateProviderProps {
    children: ReactNode;
}

function TemplateProvider({ children }: TemplateProviderProps) {
    const suggestRef = useRef<HTMLDivElement>(null);
    const [message, setMessage] = useState<MessageType | string | null>(null);
    const [confirm, setConfirm] = useState<ConfirmType | null>(null);
    const [templateLoading, setTemplateLoading] = useState<boolean>(false);

    const value = useMemo(
        () => ({
            suggestRef,
            message,
            setMessage,
            confirm,
            setConfirm,
            // when textAction button click and until textAction operation success or fail
            templateLoading,
            setTemplateLoading
        }),
        [message, confirm, templateLoading]
    );

    return (
        <TemplateContext.Provider value={value}>
            {children}
            <MessageTemplate message={message} setMessage={setMessage} />
            <ConfirmTemplate confirm={confirm} setConfirm={setConfirm} loading={templateLoading} />
        </TemplateContext.Provider>
    );
}

export default TemplateProvider;
