import copyToClipboard from '../../../../utils/copyToClipboard';
import { IconAppId, IconNotification } from '../../../../utils/SVGIcon';
import ConfigRow from './ConfigRow';
import ConfigSMTP from './ConfigSMTP';

// Define types for the component props
interface ConfigData {
    appKey?: string;
    defaultDirectory?: string;
    notificationEmail?: string;
    smtp?: any;
    [key: string]: any;
}

interface ConfigTabBasicProps {
    configsData: ConfigData;
    openModal: (type: string, data?: any) => void;
}

function ConfigTabBasic({ configsData, openModal }: ConfigTabBasicProps) {
    return (
        <>
            <ConfigRow
                icon={<IconAppId className="" />}
                title="App ID"
                // value={configsData?.appKey}
                value={
                    <span
                        role="button"
                        tabIndex={-1}
                        data-tooltip-id="myToolTip"
                        data-tooltip-content="Copy"
                        onClick={(ev) => copyToClipboard(ev, configsData?.appKey)}
                    >
                        {configsData?.appKey}
                    </span>
                }
                openModal={() => {}}
                className="[&>div:first-child]:bg-[#D6FDFF]"
            />
            <ConfigRow
                title="Default Directory"
                value={configsData?.defaultDirectory}
                openModal={() => openModal('default_dir')}
                showModify
                className=""
            />
            <ConfigRow
                title="Notification Email"
                icon={<IconNotification className="" />}
                value={configsData?.notificationEmail}
                openModal={() => openModal('notif_email')}
                showModify
                explore={false}
                className="[&>div:first-child]:bg-[#F7E6F8]"
            />

            <ConfigSMTP data={configsData.smtp} openModal={openModal} />
        </>
    );
}

export default ConfigTabBasic;
