import { ChangeEvent, useMemo, useState } from 'react';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { ButtonPrimary } from '../../../../utils/Button';
import InputField, { TextareaField } from '../../../../utils/InputField';
import { OperationFeatureRequest } from '../FeatureOperation';

interface FormData {
    fullName: string;
    email: string;
    title: string;
    description: string;
}

interface DividerProps {
    title: string;
}

const initialStateData: FormData = {
    fullName: '',
    email: '',
    title: '',
    description: ''
};

function Divider({ title }: DividerProps) {
    return (
        <div className="flex items-center gap-2.5">
            <div className="whitespace-nowrap text-base text-primary">{title}</div>
            <hr className="w-full text-primary" />
        </div>
    );
}

function RequestForm() {
    const [formData, setFormData] = useState<FormData>(initialStateData);
    const [loading, setLoading] = useState<boolean>(false);

    const { setMessage } = useTemplate();

    const onChange = (ev: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        if (ev) {
            setFormData((prev) => ({ ...prev, [ev.target.name]: ev.target.value }));
        }
    };

    const payload = useMemo(() => {
        return {
            fullName: formData.fullName,
            email: formData.email,
            title: formData.title,
            description: formData.description
        };
    }, [formData.fullName, formData.description, formData.title, formData.email]);

    const handleSendClick = () => {
        setLoading(true);
        OperationFeatureRequest(payload)
            .then((response) => {
                setMessage(response.message || 'Request sent successfully');

                // -- reset form state
                setFormData(initialStateData);

                // -- loading false
                setLoading(false);
            })
            .catch((err: any) => {
                setMessage(err?.toString());

                // -- loading false
                setLoading(false);
            });
    };

    return (
        <div className="space-y-3">
            <Divider title="Basic Information" />
            <div className="flex items-center-justify-between gap-3">
                <InputField
                    label="Full Name"
                    name={'fullName'}
                    placeholder="Enter full name..."
                    formData={formData}
                    onChange={onChange}
                />
                <InputField
                    label="Email"
                    name="email"
                    placeholder="Enter email..."
                    formData={formData}
                    onChange={onChange}
                />
            </div>
            <Divider title="Feature Information" />
            <InputField
                label="Feature Title"
                name="title"
                placeholder="Enter feature title..."
                formData={formData}
                onChange={onChange}
            />
            <TextareaField
                label="Description"
                name="description"
                placeholder="Enter feature description..."
                formData={formData}
                onChange={onChange}
                className="h-36"
                rest={{}}
            />

            <div className="flex justify-end w-full !mt-5">
                <ButtonPrimary
                    title="Send"
                    loading={loading}
                    onClick={handleSendClick}
                    disabled={false}
                />
            </div>
        </div>
    );
}

export default RequestForm;
