import cn from '../../../../utils/cn';

interface Tab {
    id: string;
    title: string;
}

const tabs: Tab[] = [
    {
        id: 'basic',
        title: 'Basic'
    },
    {
        id: 'dump-path',
        title: 'Dump Path'
    }
];

interface TabItemProps {
    title: string;
    onClick: () => void;
    active: boolean;
}

function TabItem({ title, onClick, active }: TabItemProps) {
    return (
        <li
            className={cn(
                'w-auto max-h-9 px-3 border-b-2 border-secondary-200 hover:border-secondary cursor-pointer',
                {
                    'text-primary hover:text-secondary': true,
                    'text-secondary border-secondary': active
                }
            )}
        >
            <button type="button" onClick={onClick}>
                {title}
            </button>
        </li>
    );
}

interface ConfigTabsProps {
    tabClick: (tabId: string) => void;
    activeTab: string;
}

function ConfigTabs({ tabClick, activeTab }: ConfigTabsProps) {
    return (
        <div className="flex items-center mb-3">
            <ul className="w-full flex items-center">
                {tabs?.map((tab) => (
                    <TabItem
                        key={tab?.id}
                        title={tab.title}
                        onClick={() => tabClick(tab.id)}
                        active={tab.id === activeTab}
                    />
                ))}
            </ul>
        </div>
    );
}

export default ConfigTabs;
