import { useState } from 'react';
import { Tooltip } from 'react-tooltip';
import { useApp } from '../context/AppProvider';
import cn from '../utils/cn';
import { SectionController } from '../utils/SectionController';
import ConfigSetupWizard from './Features/Wizard/ConfigSetupWizard';
import { useConfig } from './Section/Config/ConfigProvider';
import LoginSection from './Section/Login/LoginSection';
import SidebarMenu from './Sidebar/SidebarMenu';

function FooterText(): JSX.Element {
    const handleOpenLink = (): void => {
        window.electronAPI?.openLink('https://www.bikiran.com/');
    };

    return (
        <p className="w-[calc(100%_-_256px)] h-10 flex items-center justify-center fixed bottom-0 text-center text-sm">
             All Rights Reserved By &nbsp;
            <button type="button" className="text-secondary" onClick={handleOpenLink}>
                bikiran.com
            </button>
        </p>
    );
}

function Content(): JSX.Element {
    const [showWizard, setShowWizard] = useState<number>(0);
    const { /* isConfigured, */ handleReload } = useConfig();

    // -- check if default directory is set or not
    // useEffect(() => {
    //     if (!isConfigured) {
    //         setShowWizard(1);
    //     }
    // }, [isConfigured]);

    const { selectedMenu } = useApp();

    const closeWizard = (): void => {
        setShowWizard(0);
        handleReload();
    };

    return (
        <div className={cn(`w-[calc(100%_-_256px)] h-[calc(100%_-_40px)]`)}>
            {SectionController(selectedMenu?.section)}

            {/* setup wizard */}
            {showWizard ? <ConfigSetupWizard closeWizard={closeWizard} /> : null}

            <FooterText />
        </div>
    );
}

function AppContent(): JSX.Element {
    const { selectedMenu } = useApp();
    const section = selectedMenu?.section;
    const prevPath = selectedMenu?.prev;
    if (section === 'login') {
        return <LoginSection prevPath={prevPath || undefined} />;
    }
    return (
        <main className="w-full h-screen flex border-t border-[rgba(19,15,64,0.20)]">
            <div className="w-64 h-full border-r border-[rgba(19,15,64,0.20)]">
                <SidebarMenu />
            </div>

            <Content />
            <Tooltip id="myToolTip" style={{ zIndex: '9999999999' }} />
        </main>
    );
}

export default AppContent;
