/* eslint-disable no-underscore-dangle */
import { useState } from 'react';
import { useApp } from '../../../../context/AppProvider';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { DestinationData } from '../../../../data/types';
import { OptionButton } from '../../../../utils/Button';
import InstOption from '../../../../utils/InstOption';
import { CloudIcon } from '../../../../utils/SVGIcon';
import cn from '../../../../utils/cn';

interface DestinationItemProps {
    data: DestinationData;
    handleReload: (() => void) | null;
}

interface ApiResponse {
    error: number;
    message?: string;
    data?: any;
}

function DestinationItem({ data, handleReload }: DestinationItemProps): JSX.Element {
    const [sourceId, setSourceId] = useState<string>('');

    const { setMessage, setConfirm, setTemplateLoading } = useTemplate();

    const { openModal } = useApp();

    const closeMenu = (): void => {
        setSourceId('');
    };

    const handleDelete = (): void => {
        const payload = {
            ...data
        };

        setConfirm({
            text: `Are you sure you want to delete this destination?`,
            textAction: 'Yes',
            clickAction: () => {
                // -- Conifirm button loading true
                setTemplateLoading(true);

                window.electronAPI
                    ?.deleteDestination(payload)
                    .then(({ error, message }: ApiResponse) => {
                        setMessage(message || '');

                        // -- Conifirm button loading false
                        setTemplateLoading(false);

                        if (error === 0) {
                            setConfirm(null);
                            if (handleReload) {
                                handleReload();
                            }
                        }
                    })
                    .catch((err: any) => {
                        setMessage(err?.toString() || 'An error occurred');

                        // -- Conifirm button loading false
                        setTemplateLoading(false);
                    });
            }
        });
    };

    // -- Check if the destination is default
    const isDefault = data?._id === 'default';

    return (
        <div
            className={cn(`list_item`, {
                'placeholder-mode': data?.demo
            })}
        >
            <div>
                <CloudIcon className="bg-[#D3F6EE]" />
            </div>

            <div>
                <div className="flex items-center gap-x-2">
                    <span className="text-sm text-primary font-medium capitalize">
                        {data?.title}
                    </span>
                    <span
                        className="border border-secondary rounded-30 px-1 text-xs text-secondary"
                        hidden={!isDefault}
                    >
                        Default
                    </span>
                </div>
                <p className="text-xs text-primary-700">
                    <span>{data?.type}</span>
                </p>
            </div>

            {!isDefault ? (
                <div>
                    <OptionButton className="size-9" onClick={() => setSourceId(data?._id)} />

                    <InstOption
                        showMenu={sourceId === data?._id}
                        setShowMenu={closeMenu}
                        className="top-[53px] -right-2"
                    >
                        <button type="button" onClick={handleDelete}>
                            Delete
                        </button>
                        <button type="button" onClick={() => openModal(`test_${data?.type}`, data)}>
                            Test
                        </button>
                    </InstOption>
                </div>
            ) : (
                <div />
            )}
        </div>
    );
}

export default DestinationItem;
