import LibModal from '../../../Features/LibModal/LibModal';
import TypeMsSqlHost from './Comp/TypeMsSqlHost';
import TypeMsSqlWindows from './Comp/TypeMsSqlWindows';
import TypeMySqlHost from './Comp/TypeMySqlHost';
import TypeMySqlWindows from './Comp/TypeMySqlWindows';
import TypePgSql from './Comp/TypePgSql';

function ModalController(
    type,
    data,
    setOpen,
    handelCreateSource,
    handleUpdateSource,
    handleUpdateFrequency,
    loading
) {
    switch (type) {
        case 'mysql-win':
            return (
                <LibModal title="MySQL" setOpen={setOpen}>
                    <TypeMySqlWindows
                        type="mysql-win"
                        onSave={handelCreateSource}
                        setOpen={setOpen}
                        loading={loading}
                    />
                </LibModal>
            );
        case 'update_mysql-win':
            return (
                <LibModal title="Update MySQL" setOpen={setOpen}>
                    <TypeMySqlWindows
                        data={data}
                        onSave={handleUpdateSource}
                        setOpen={setOpen}
                        loading={loading}
                    />
                </LibModal>
            );
        case 'mysql':
            return (
                <LibModal title="MySQL" setOpen={setOpen}>
                    <TypeMySqlHost
                        type="mysql"
                        onSave={handelCreateSource}
                        setOpen={setOpen}
                        loading={loading}
                    />
                </LibModal>
            );
        case 'update_mysql':
            return (
                <LibModal title="Update MySQL" setOpen={setOpen}>
                    <TypeMySqlHost
                        data={data}
                        onSave={handleUpdateSource}
                        setOpen={setOpen}
                        loading={loading}
                    />
                </LibModal>
            );
        case 'mssql-win':
            return (
                <LibModal title="MsSQL(Win Auth)" setOpen={setOpen} className="!w-100">
                    <TypeMsSqlWindows
                        type="mssql-win"
                        onSave={handelCreateSource}
                        setOpen={setOpen}
                        loading={loading}
                    />
                </LibModal>
            );
        case 'update_mssql-win':
            return (
                <LibModal title="Update MsSQL(Windows)" setOpen={setOpen}>
                    <TypeMsSqlWindows
                        data={data}
                        onSave={handleUpdateSource}
                        setOpen={setOpen}
                        loading={loading}
                    />
                </LibModal>
            );
        case 'mssql-host':
            return (
                <LibModal title="MsSQL(Password Auth)" setOpen={setOpen}>
                    <TypeMsSqlHost
                        type="mssql-host"
                        onSave={handelCreateSource}
                        setOpen={setOpen}
                        loading={loading}
                    />
                </LibModal>
            );
        case 'update_mssql-host':
            return (
                <LibModal title="Update MsSQL(Host)" setOpen={setOpen}>
                    <TypeMsSqlHost
                        data={data}
                        onSave={handleUpdateSource}
                        setOpen={setOpen}
                        loading={loading}
                    />
                </LibModal>
            );
        case 'pgsql':
            return (
                <LibModal title="Postgres SQL" setOpen={setOpen}>
                    <TypePgSql
                        type="pgsql"
                        onSave={handelCreateSource}
                        setOpen={setOpen}
                        loading={loading}
                    />
                </LibModal>
            );
        case 'update_pgsql':
            return (
                <LibModal title="Update Postgres SQL" setOpen={setOpen}>
                    <TypePgSql
                        data={data}
                        onSave={handleUpdateSource}
                        setOpen={setOpen}
                        loading={loading}
                    />
                </LibModal>
            );

        default:
            return null;
    }
}

export default ModalController;
