import { useState } from 'react';
import { useTemplate } from '../../../context/template/TemplateProvider';
import { DestinationData } from '../../../data/types';
import { ButtonPrimary } from '../../../utils/Button';

interface InfoRowProps {
    title: string;
    value?: string;
    onClick?: () => void;
}

function InfoRow({ title, value, onClick }: InfoRowProps): JSX.Element {
    return (
        <div>
            <span className="block text-base text-primary font-medium">&bull; {title}</span>
            <span className="text-primary-700 text-sm ml-3">{value || '[Not Set]'}</span>
            {onClick && (
                <button
                    type="button"
                    className="text-secondary text-sm underline ml-2"
                    onClick={onClick}
                >
                    {value ? 'Update' : 'Set'}
                </button>
            )}
        </div>
    );
}

interface ConfigData {
    appKey?: string;
    defaultDirectory?: string;
    notificationEmail?: string;
}

interface SetupElementsProps {
    data?: ConfigData;
    destinationData?: DestinationData[];
    openModal: (type: string, data?: any) => void;
    closeWizard: () => void;
}

function SetupElements({
    data,
    destinationData,
    openModal,
    closeWizard
}: SetupElementsProps): JSX.Element {
    const [loading, setLoading] = useState<boolean>(false);
    const { setMessage } = useTemplate();
    // eslint-disable-next-line no-underscore-dangle
    const defaultDestination = destinationData?.find((d) => d._id === 'default');

    const defaultDir = data?.defaultDirectory?.length;
    const notificationEmail = data?.notificationEmail?.length;

    const handleFinish = (): void => {
        setLoading(true);
        window.electronAPI
            ?.reloadWindow()
            .then(({ error }: { error: number }) => {
                setLoading(false);

                if (error === 0) {
                    closeWizard();
                }
            })
            .catch((err: any) => {
                setLoading(false);
                setMessage(err.toString());
                console.log(err);
            });
    };

    return (
        <div className="h-full p-5 space-y-5">
            <InfoRow title="Your APP ID" value={data?.appKey} />
            <InfoRow
                title="Default Directory"
                value={data?.defaultDirectory}
                onClick={() => openModal('default_dir', data?.defaultDirectory)}
            />
            <InfoRow
                title="Notification Email"
                value={data?.notificationEmail}
                onClick={() => openModal('notif_email', data)}
            />
            <InfoRow title="Default Destination" value={defaultDestination?.type} />

            <div className="w-full flex justify-center">
                <ButtonPrimary
                    title="Finish"
                    disabled={
                        !defaultDir || !notificationEmail || !defaultDestination?.type?.length
                    }
                    loading={loading}
                    onClick={handleFinish}
                />
            </div>
        </div>
    );
}

export default SetupElements;
