function truncateString(str: string, num: number = 10): string {
    if (str && str.length > 25) {
        // Truncate the string to keep the first 20 characters and the last 20 characters
        return `${str.slice(0, num)}............${str.slice(-num)}`;
    }
    return str; // Return original string if it's 25 characters or shorter
}

export default truncateString;
