import { ReactElement } from 'react';
import { RedirectIcon } from '../../../utils/SVGIcon';
import cn from '../../../utils/cn';

interface MenuProps {
    title: string;
    icon: ReactElement;
    isActive: boolean;
    isRedirect: boolean;
    onMenuClick: () => void;
    className: string;
}

function Menu({
    title,
    icon,
    isActive,
    isRedirect,
    onMenuClick,
    className
}: MenuProps): JSX.Element {
    return (
        <button
            type="button"
            className={cn(
                `group flex gap-x-4 items-center w-full h-[45px] py-2.5 px-[15px] rounded-[8px]`,
                { className },
                {
                    'bg-secondary-50': isActive,
                    'hover:bg-secondary-50': !isActive
                }
            )}
            onClick={onMenuClick}
        >
            <span
                className={cn(`size-[25px] text-primary-700 group-hover:text-primary`, {
                    'text-primary': isActive
                })}
            >
                {icon}
            </span>
            <div className="flex items-center gap-x-2">
                <h2
                    className={cn(`text-primary-700 group-hover:text-primary text-base`, {
                        'text-primary': isActive
                    })}
                >
                    {title}
                </h2>
                {isRedirect ? <RedirectIcon className="size-3" /> : null}
            </div>
        </button>
    );
}

export default Menu;
