/* eslint-disable no-underscore-dangle */
import { useState } from 'react';
import { useApp } from '../../../../context/AppProvider';
import { ButtonPrimary, OptionButton } from '../../../../utils/Button';
import InstOption from '../../../../utils/InstOption';
import { UploadIcon } from '../../../../utils/SVGIcon';
import formatBytes from '../../../../utils/formatBytes';

function SourceManageOptions({
    data,
    isUploading,
    handleDeleteSource,
    handleForceBackup,
    handleCleanupBackup,
    openModal
}) {
    // -- State to store source id
    const [sourceId, setSourceId] = useState(0);

    const { navigate } = useApp();

    // close menu
    const closeMenu = () => {
        setSourceId(0);
    };

    return (
        <div className="flex items-center gap-x-2 !cursor-default !w-[240px]">
            {data?.uploads ? (
                <ButtonPrimary
                    title={`${data?.uploads}/100 \u2022 ${formatBytes(data?.uploadsFileSize)}`}
                    className="whitespace-nowrap !min-w-[110px] !w-auto bg-[rgba(20,185,255,0.20)] text-[#14B9FF]"
                    onClick={(ev) => ev.stopPropagation() + navigate('backups', data)}
                />
            ) : null}
            <button
                type="button"
                onClick={(ev) => handleForceBackup(ev, data?._id)}
                data-tooltip-id="myToolTip"
                data-tooltip-content="Force Backup"
                disabled={isUploading}
                className="rounded-10"
            >
                <UploadIcon className="bg-[#14B9FF] rounded-10 p-[4px]" />
            </button>

            <div className="relative">
                <OptionButton className="size-9" onClick={() => setSourceId(data?._id)} />

                <InstOption
                    showMenu={sourceId === data?._id}
                    setShowMenu={closeMenu}
                    className="w-44 top-[53px] -right-2"
                >
                    <button
                        type="button"
                        onClick={(ev) =>
                            ev.stopPropagation() + openModal(`update_${data?.type}`, data)
                        }
                    >
                        Modify Connection
                    </button>
                    <button
                        type="button"
                        onClick={(ev) => ev.stopPropagation() + openModal(`set_frequency`, data)}
                    >
                        Set Frequency
                    </button>
                    {/* <button type="button" onClick={(ev) => ev.stopPropagation() + openModal(`link_destination`, data)}>
                            Link Destination
                        </button> */}
                    <button
                        type="button"
                        onClick={(ev) => ev.stopPropagation() + navigate('backups', data)}
                    >
                        Show Backups
                    </button>
                    <button
                        type="button"
                        onClick={(ev) => handleCleanupBackup(ev, { sourceId: data?._id })}
                    >
                        Cleanup Backups
                    </button>

                    <button
                        type="button"
                        onClick={(ev) => ev.stopPropagation() + handleDeleteSource(data)}
                    >
                        Delete Source
                    </button>
                </InstOption>
            </div>
        </div>
    );
}

export default SourceManageOptions;
