import React, { useState } from 'react';
import { ButtonCancel, ButtonPrimary } from '../../../../utils/Button';
import { CloudIcon } from '../../../../utils/SVGIcon';
import FileUploadArea from '../../../Features/FileUpload/FileUploadArea';

interface GoogleBucketData {
    _id?: string;
    type?: string;
    title?: string;
    location?: string;
    projectId?: string;
    credentials?: any;
    bucketName?: string;
}

interface AddGoogleBucketModalProps {
    closeModal: () => void;
    data?: GoogleBucketData;
}

function CloudInfo(): JSX.Element {
    return (
        <div className="mb-3">
            <div className="flex items-center gap-x-2">
                <div className="w-8 h-8 bg-[#D3F6EE] rounded-full flex items-center justify-center">
                    <CloudIcon className="w-4 h-4 text-blue-500" />
                </div>
                <div className="text-lg font-semibold">Google Bucket</div>
            </div>
            <div className="text-sm text-gray-500">
                Google Bucket is a cloud storage service that allows you to store and serve large
                amounts of data.
            </div>
        </div>
    );
}

function AddGoogleBucketModal({ closeModal, data }: AddGoogleBucketModalProps): JSX.Element {
    const [fileDirectory, setFileDirectory] = useState<string>('');
    const [loading, setLoading] = useState<boolean>(false);

    const handleChooseFile = (): void => {
        setLoading(true);
        window.electronAPI
            ?.openFileDialog()
            .then((res: string) => {
                if (res) {
                    setFileDirectory(res);
                }
                setLoading(false);
            })
            .catch((error: any) => {
                console.log(error);
                setLoading(false);
            });
    };

    const handleReset = (ev: React.MouseEvent): void => {
        ev.preventDefault();
        setFileDirectory('');
    };

    const handleSave = (): void => {
        console.log(fileDirectory);
    };

    return (
        <div>
            <div className="modal-content">
                <div className="modal-body mb-3">
                    <CloudInfo />

                    <div className="text-sm text-primary mb-2">
                        Upload .json file of your google bucket configuration:
                    </div>

                    <FileUploadArea
                        handleChooseFile={handleChooseFile}
                        handleReset={handleReset}
                        choosenFile={fileDirectory}
                        loading={loading}
                        className=""
                    />
                </div>
                <div className="flex items-center justify-end gap-x-2">
                    <ButtonCancel title="Cancel" onClick={closeModal} disabled={false}>
                        Cancel
                    </ButtonCancel>
                    <ButtonPrimary title="Save" onClick={handleSave} disabled={false} />
                </div>
            </div>
        </div>
    );
}

export default AddGoogleBucketModal;
