import React, { useState } from 'react';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { ButtonCancel, ButtonPrimary } from '../../../../utils/Button';
import InputField from '../../../../utils/InputField';

interface SMTPTestData {
    hostname?: string;
    port?: string;
    username?: string;
    password?: string;
}

interface FormData {
    hostname: string;
    port: string;
    username: string;
    password: string;
}

interface ModalSMTPConfigUpdateProps {
    closeClick: () => void;
    data?: SMTPTestData;
}

function ModalSMTPConfigUpdate({ closeClick, data }: ModalSMTPConfigUpdateProps): JSX.Element {
    const [formData, setFormData] = useState<FormData>({
        hostname: data?.hostname || '',
        port: data?.port || '',
        username: data?.username || '',
        password: data?.password || ''
    });
    const [loading, setLoading] = useState<boolean>(false);
    const { setMessage } = useTemplate();

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setFormData((prev) => ({
            ...prev,
            [name]: value
        }));
    };

    const handleTest = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        setLoading(true);
        window.electronAPI
            ?.testSMTPConfig(formData)
            .then(({ error, message }) => {
                setLoading(false);
                setMessage(message || '');
                if (!error) {
                    closeClick();
                }
            })
            .catch((err: any) => {
                setLoading(false);
                setMessage(err?.toString() || 'An error occurred');
            });
    };
    return (
        <form onSubmit={handleTest}>
            <div className="flex flex-col gap-y-2 mb-4">
                <InputField
                    label="Hostname"
                    name="hostname"
                    placeholder="Type Hostname..."
                    formData={formData}
                    onChange={handleChange}
                    readOnly
                />
                <InputField
                    label="Port"
                    name="port"
                    placeholder="Type Port..."
                    formData={formData}
                    onChange={handleChange}
                    readOnly
                />
                <InputField
                    label="Username"
                    name="username"
                    placeholder="Type Username..."
                    formData={formData}
                    onChange={handleChange}
                    readOnly
                />
                <InputField
                    label="Test Email"
                    name="TestEmail"
                    placeholder="Ex: test@example.com"
                    formData={formData}
                    onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                        setFormData((d) => ({ ...d, TestEmail: e.target.value }))
                    }
                />
            </div>

            <div className="flex gap-x-2 justify-end">
                <ButtonCancel title="Cancel" onClick={() => closeClick()} disabled={false}>
                    Cancel
                </ButtonCancel>
                <ButtonPrimary
                    title="Send"
                    btnType="submit"
                    loading={loading}
                    onClick={() => {}}
                    disabled={false}
                />
            </div>
        </form>
    );
}

export default ModalSMTPConfigUpdate;
