import { FileIcon, IconMsSql, IconMySql, IconPgSql } from './SVGIcon';

export const iconHandler = (type: string): JSX.Element => {
    // using switch
    switch (type) {
        case 'directory':
            return <FileIcon className="" />;
        case 'mysql':
            return <IconMySql className="" />;
        case 'mssql-win':
            return <IconMySql className="" />;
        case 'mssql-host':
            return <IconMsSql className="" />;
        case 'pgsql':
            return <IconPgSql className="" />;
        default:
            return <FileIcon className="" />;
    }
};

export const sourceBgHandler = (type: string): string => {
    // using switch
    switch (type) {
        case 'directory':
            return 'bg-[#FFF3D6]';
        case 'mysql':
            return 'bg-[#FDEFDB]';
        case 'mssql-win':
            return 'bg-[#FDEFDB]';
        case 'mssql-host':
            return 'bg-[#FFEBEB]';
        case 'pgsql':
            return 'bg-[rgba(51,103,145,0.10)]';
        default:
            return 'bg-blue-500';
    }
};
