/* eslint-disable no-underscore-dangle */
import { createContext, ReactNode, useContext, useEffect, useMemo, useState } from 'react';
import '../../../app/types/electron'; // Import electron types
import { useApp } from '../../../context/AppProvider';
import { useTemplate } from '../../../context/template/TemplateProvider';
import { destinationDemoData } from '../../../data/destinationDemoData';
import { DestinationData } from '../../../data/types';
import useConfigOperationHandler from './useConfigOperationHandler';

// Define types for configuration data
interface ConfigData {
    appKey: string;
    app_key: string;
    defaultDirectory: string;
    default_directory: string;
    notificationEmail: string;
    dumpMssql: string;
    dumpMysql: string;
    dumpPgsql: string;
    demo: boolean;
    [key: string]: any;
}

// Define the context type
interface ConfigContextType {
    appId: string;
    isConfigured: boolean;
    handleSetDumpPath: (type: string, path: string, modal?: string) => void;
    handleReload: () => void;
    reloadDestination: () => void;
    handleSaveDirectory: () => void;
    handleLocalJsonImport: () => void;
    handleCloudConfigJsonImport: () => void;
    handleConfigExport: (ev: any) => void;
    loading: boolean;
    destinationData: DestinationData[];
    configsData: ConfigData;
    setSelectedPath: (path: string) => void;
    selectedPath: string;
    defaultDir: boolean;
}

const ConfigContext = createContext<ConfigContextType | undefined>(undefined);

export const useConfig = (): ConfigContextType => {
    const context = useContext(ConfigContext);
    if (context === undefined) {
        throw new Error('useConfig must be used within a ConfigProvider');
    }
    return context;
};

// --Initial value for default directory and set demo to true to show placeholder
const initialValue: ConfigData = {
    appKey: '',
    app_key: '',
    defaultDirectory: '',
    default_directory: '',
    notificationEmail: '',
    dumpMssql: '',
    dumpMysql: '',
    dumpPgsql: '',
    demo: true
};

interface ConfigProviderProps {
    children: ReactNode;
}

function ConfigProvider({ children }: ConfigProviderProps) {
    // --State for store default directory data
    const [configsData, setConfigsData] = useState(initialValue);

    // --State to store destination data
    const [destinationData, setDestinationData] = useState(destinationDemoData);

    // --State for store selected path
    const [selectedPath, setSelectedPath] = useState('');

    // --State for store loading status white adding or updating default directory
    const [loading, setLoading] = useState(false);
    // --State for store reload key
    const [reloadKey, setReloadKey] = useState(0);
    const [reloadKey2, setReloadKey2] = useState(0);

    const { closeModal } = useApp();

    const { setMessage } = useTemplate();

    // find app id
    const appId = configsData?.appKey || '';
    // get default backup directory
    useEffect(() => {
        setConfigsData(initialValue);
        window?.electronAPI
            ?.getConfigs()
            .then(({ data }: { data?: ConfigData } = {}) => {
                setConfigsData(data || initialValue);
            })
            .catch((err: any) => {
                // -- set default directory to initial value
                setConfigsData(initialValue);
                // -- set error message
                setMessage(err.message);
            });
    }, [setMessage, reloadKey]);

    // Fetch sources data
    useEffect(() => {
        setDestinationData(destinationDemoData);
        window?.electronAPI
            ?.getDestinations()
            .then(({ data }: { data?: DestinationData[] } = {}) => {
                setDestinationData(data || []);
            })
            .catch((err: any) => {
                setDestinationData([]);
                setMessage(err?.toString());
                console.log(err);
            });
    }, [setMessage, reloadKey2]);

    const handleReload = () => {
        setReloadKey((prev) => prev + 1);
    };

    const handleCancelClick = () => {
        setSelectedPath('');
        closeModal();
    };

    const {
        handleSaveDirectory,
        handleLocalJsonImport,
        handleCloudConfigJsonImport,
        handleConfigExport,
        handleSetDumpPath
    } = useConfigOperationHandler(
        handleReload,
        handleCancelClick,
        selectedPath,
        setSelectedPath,
        configsData,
        setLoading,
        setMessage,
        appId
    );

    const value = useMemo(() => {
        const reloadDestination = () => {
            setReloadKey2((prev) => prev + 1);
        };

        // check if all configurations are set or not
        const defaultDir = configsData?.defaultDirectory?.length > 0;
        const notificationEmail = configsData?.notificationEmail?.length > 0;

        const isConfigured = defaultDir && notificationEmail;

        return {
            appId,
            isConfigured,
            handleSetDumpPath,
            handleReload,
            reloadDestination,
            handleSaveDirectory,
            handleLocalJsonImport,
            handleCloudConfigJsonImport,
            handleConfigExport,
            loading,
            destinationData,
            configsData,
            setSelectedPath,
            selectedPath,
            defaultDir
        };
    }, [
        configsData,
        appId,
        handleSetDumpPath,
        handleSaveDirectory,
        handleLocalJsonImport,
        handleCloudConfigJsonImport,
        handleConfigExport,
        loading,
        destinationData,
        selectedPath
    ]);
    return <ConfigContext.Provider value={value}>{children}</ConfigContext.Provider>;
}

export default ConfigProvider;
