const copyToClipboard = (event: React.MouseEvent<HTMLElement>, value: string | undefined): void => {
    if (value) {
        navigator.clipboard.writeText(value);
        const element = event.target as HTMLElement;
        element.dataset.tooltipContent = 'Copied'; // Set tooltip content to 'Copied'
        setTimeout(() => {
            element.dataset.tooltipContent = 'Copy'; // Set tooltip content back to 'Copy' after 1 second
        }, 1000);
    }
};

export default copyToClipboard;
