interface ConfigDumpItemProps {
    title: string;
    value: string | ReactNode;
    icon?: ReactNode;
    type: string;
    chooseFileClick: (type: string, subType: string) => void;
    testClick: (type: string, value: string | ReactNode) => void;
    className?: string;
}

function ConfigDumpItem({
    title,
    value,
    icon,
    type,
    chooseFileClick,
    testClick,
    className
}: ConfigDumpItemProps) {
    return (
        <div className={cn('list_item !mt-0', className)}>
            <div className="rounded-10">
                {icon || <CloudIcon className="" />}
                {/* <CloudIcon /> */}
            </div>

            <div>
                <Col2 title={title} value={value} />
            </div>

            <div className={cn(`items-center gap-x-2`)}>
                <div>
                    <button
                        type="button"
                        className="bg-secondary-100 hover:bg-secondary rounded-5 h-7 px-2 py-1 text-sm text-secondary hover:text-white"
                        onClick={() => testClick(type, value)}
                    >
                        Test
                    </button>
                </div>
                {/* <ManageMenu
                    chooseFileClick={() => chooseFileClick('dump-path', type)}
                    type={type}
                    value={value}
                /> */}
            </div>
        </div>
    );
}

