/* eslint-disable import/no-extraneous-dependencies */
import { useRef } from 'react';
import { ButtonCancel, ButtonConfirm, ButtonPrimary } from '../../utils/Button';
import { WarningIcon } from '../../utils/SVGIcon';

// Define types for confirm object
interface ConfirmType {
    icon?: string;
    text?: string;
    buttons?: string[];
    showButton?: boolean;
    showIcon?: boolean;
    textCancel?: string;
    textAction?: string;
    clickAction?: (confirm: ConfirmType, setConfirm: (confirm: ConfirmType | null) => void) => void;
}

interface ButtonsProps {
    confirm: ConfirmType;
    setConfirm: (confirm: ConfirmType | null) => void;
    buttons?: string[];
    loading: boolean;
}

function Buttons({ confirm, setConfirm, buttons, loading }: ButtonsProps) {
    if (confirm.buttons) {
        return buttons?.map((button, index) => (
            <div className="template-clickAction" key={`btn-${index}`}>
                <ButtonPrimary
                    title={button}
                    disabled={false}
                    onClick={() => {
                        if (typeof confirm.clickAction === 'function') {
                            confirm.clickAction(confirm, setConfirm);
                        } else {
                            setConfirm(null);
                        }
                    }}
                />
            </div>
        ));
    }
    return (
        <div className="flex flex-row justify-center items-center gap-4">
            <ButtonCancel
                onClick={() => setConfirm(null)}
                title={confirm.textCancel || 'Cancel'}
                disabled={false}
            >
                {confirm.textCancel || 'Cancel'}
            </ButtonCancel>

            <ButtonConfirm
                onClick={() => {
                    if (typeof confirm.clickAction === 'function') {
                        confirm.clickAction(confirm, setConfirm);
                    }
                }}
                title={confirm.textAction || 'Delete'}
                disabled={false}
                loading={loading}
            >
                {confirm.textAction || 'Delete'}
            </ButtonConfirm>
        </div>
    );
}

interface ConfirmTemplateProps {
    setConfirm: (confirm: ConfirmType | null) => void;
    confirm: ConfirmType | null;
    loading: boolean;
}

function ConfirmTemplate({ setConfirm, confirm, loading }: ConfirmTemplateProps) {
    // setConfirm
    const ref = useRef<HTMLDivElement>(null);
    const { icon, text, buttons, showButton = true, showIcon = true } = confirm || {};

    if (!confirm) {
        return null;
    }

    return (
        <div className="modal-box fixed bg-primary/50 top-0 left-0 px-4 w-full h-full flex flex-col justify-center items-center z-50">
            <div className="modal-box-content w-80 md:w-104 min-h-46 md:min-h-56 bg-white-100 p-5 md:p-7 rounded-3xl text-center">
                <div className="flex flex-col justify-center items-center gap-3 md:gap-5" ref={ref}>
                    {showIcon && (
                        <div className="size-10 md:size-12 text-warning">
                            {icon ? (
                                <img src={icon} alt="" className="block" />
                            ) : (
                                <WarningIcon className="" />
                            )}
                        </div>
                    )}

                    <div className="font-medium text-sm md:text-base w-full text-center">
                        {text || '--'}
                    </div>

                    {showButton && (
                        <div className="flex flex-row justify-center items-center gap-4">
                            <Buttons
                                setConfirm={setConfirm}
                                confirm={confirm}
                                buttons={buttons}
                                loading={loading}
                            />
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}

export default ConfirmTemplate;
