/* eslint-disable no-underscore-dangle */
import { createContext, useContext, useEffect, useMemo, useState } from 'react';
import { useTemplate } from '../../../context/template/TemplateProvider';
import { sourceDemoData } from '../../../data/sourceDemoData';
import useSourceOperationHandler from './useSourceOperationHandler';

const SourceContext = createContext();

export const useSource = () => useContext(SourceContext);

function SourceProvider({ children }) {
    // --State for store directory path
    const [selectedPath, setSelectedPath] = useState('');
    // --State for store sources data
    const [sourcesData, setSourcesData] = useState(sourceDemoData);
    const [taskSt, setTaskSt] = useState([]);
    const [reloadKey, setReloadKey] = useState(0);
    // --State for loading
    const [loading, setLoading] = useState(false);

    const [dumpPathModal, setDumpPathModal] = useState({
        mode: false,
        type: ''
    });

    // state for store force backup process loading
    const [forceBackupLoading, setForceBackupLoading] = useState(false);

    const { setMessage } = useTemplate();

    // Fetch sources data
    useEffect(() => {
        // setSourcesData(sourceDemoData);
        window?.electronAPI
            ?.getSources()
            .then(({ data, tasks } = {}) => {
                setSourcesData(data || []);
                setTaskSt(tasks || []);
            })
            .catch((err) => {
                setSourcesData([]);
                setMessage(err?.toString());
            });
    }, [reloadKey, setMessage]);

    // -- handle reload sources data
    const handleReload = () => {
        setSourcesData(sourceDemoData);

        setReloadKey((prev) => prev + 1);
    };

    const {
        handelCreateSource,
        handleUpdateSource,
        handleUpdateAutoStart,
        handleUpdateFrequency,
        handleDeleteSource,
        handleStartBackup,
        handleStopBackup,
        handleForceBackup,
        handleCleanupBackup
    } = useSourceOperationHandler(
        handleReload,
        setReloadKey,
        setSelectedPath,
        setLoading,
        setForceBackupLoading
    );

    const value = useMemo(() => {
        return {
            sourcesData,
            handleReload,
            handelCreateSource,
            handleUpdateSource,
            handleUpdateAutoStart,
            handleUpdateFrequency,
            handleDeleteSource,
            setReloadKey,
            selectedPath,
            setSelectedPath,
            handleStartBackup,
            handleStopBackup,
            handleForceBackup,
            handleCleanupBackup,
            forceBackupLoading,
            setForceBackupLoading,
            loading,
            taskSt,
            dumpPathModal,
            setDumpPathModal
        };
    }, [
        dumpPathModal,
        forceBackupLoading,
        handelCreateSource,
        handleCleanupBackup,
        handleDeleteSource,
        handleForceBackup,
        handleStartBackup,
        handleStopBackup,
        handleUpdateAutoStart,
        handleUpdateFrequency,
        handleUpdateSource,
        loading,
        selectedPath,
        sourcesData,
        taskSt
    ]);
    return <SourceContext.Provider value={value}>{children}</SourceContext.Provider>;
}

export default SourceProvider;
