import { useState } from 'react';
import { ButtonCancel, ButtonPrimary } from '../../../../../utils/Button';
import InputField from '../../../../../utils/InputField';
import Loading from '../../../../Features/Loading/Loading';

function TypeMsSqlWindows({ type, data, onSave, setOpen, loading }) {
    const [formData, setFormData] = useState({
        ...(data || {}),
        type: type || data?.type,
        databaseOrPath: data?.databaseOrPath,
        operation: data?.operation || 'exec' || ''
    });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prev) => ({
            ...prev,
            [name]: value
        }));
    };

    // const { configsData } = useConfig();
    // const msSqlDumpPath = configsData?.dumpMssql || '';

    const isDisabled = !formData.databaseOrPath || loading;

    return (
        <div className="w-full h-full space-y-4">
            <Loading loading={loading} />
            <form
                onSubmit={(ev) => {
                    onSave(ev, formData);
                }}
                className="w-full space-y-2"
            >
                <InputField
                    name="databaseOrPath"
                    label="Database"
                    placeholder="Type the database name"
                    formData={formData}
                    onChange={handleChange}
                    className="w-full"
                />
                {/* <SelectOption
                    name="operation"
                    label="Operation Type"
                    formData={formData}
                    onChange={handleChange}
                    options={options?.map((item) => addOption(item.value, item.label, item.value))}
                    className="w-full !mb-3"
                /> */}
                {/* <DumpPathRow type="dumpMssql" path={msSqlDumpPath} /> */}
                <div className="w-full flex justify-end gap-x-2">
                    <ButtonCancel title="Cancel" onClick={setOpen} />
                    <ButtonPrimary title="Create" btnType="submit" disabled={isDisabled} />
                </div>
            </form>
        </div>
    );
}

export default TypeMsSqlWindows;
