import { useState } from 'react';
import { useApp } from '../../../context/AppProvider';
import { useConfig } from '../../Section/Config/ConfigProvider';
import ModalImportCloudConfigJson from '../../Section/Config/Modals/ModalImportCloudConfigJson';
import ModalImportLocalJson from '../../Section/Config/Modals/ModalImportLocalJson';
import ModalNotificationEmail from '../../Section/Config/Modals/ModalNotificationEmail';
import DirectoryModal from '../Directory/DirectoryModal';
import LibModal from '../LibModal/LibModal';
import SetupElements from './SetupElements';
import WizardHeader from './WizardHeader';

interface ContentProps {
    start: boolean;
    data: any;
    destinationData: any;
    handleStart: () => void;
    closeWizard: () => void;
    openModal: (type: string) => void;
}

function Content({
    start,
    data,
    destinationData,
    handleStart,
    closeWizard,
    openModal
}: ContentProps): JSX.Element {
    if (start) {
        return (
            <>
                <WizardHeader openModal={openModal} />
                <SetupElements
                    data={data}
                    destinationData={destinationData}
                    openModal={openModal}
                    closeWizard={closeWizard}
                />
            </>
        );
    }
    return (
        <div className="flex items-center justify-center h-full">
            <button
                type="button"
                className="text-2xl hover:bg-secondary border border-secondary hover:border-transparent text-secondary hover:text-white rounded-30 px-4 py-2"
                onClick={handleStart}
            >
                Start Setup
            </button>
        </div>
    );
}

interface ConfigSetupWizardProps {
    closeWizard: () => void;
}

function ConfigSetupWizard({ closeWizard }: ConfigSetupWizardProps): JSX.Element {
    const [start, setStart] = useState<boolean>(false);

    const {
        configsData: data,
        destinationData,
        handleSaveDirectory,
        selectedPath,
        setSelectedPath,
        handleReload,
        handleLocalJsonImport,
        handleCloudConfigJsonImport,
        loading
    } = useConfig();

    const { modalType, modalData, openModal, closeModal } = useApp();

    const handleStart = () => {
        setStart(true);
        handleReload();
        // reloadDestination();
    };

    return (
        <div className="w-full h-full fixed top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 flex items-center justify-center bg-[#292929d4] p-5g z-50">
            <div className="w-[50%] h-[65%] bg-white rounded-10">
                <Content
                    start={start}
                    data={data}
                    destinationData={destinationData}
                    handleStart={handleStart}
                    openModal={openModal}
                    closeWizard={closeWizard}
                />
            </div>

            {modalType === 'default_dir' ? (
                <LibModal
                    title={modalData ? 'Update Default Directory' : 'Set Default Directory'}
                    subTitle=""
                    className="!w-[400px]"
                    setOpen={closeModal}
                >
                    <DirectoryModal
                        path={modalData}
                        selectedPath={selectedPath}
                        setSelectedPath={setSelectedPath}
                        cancelClick={closeModal}
                        onClick={handleSaveDirectory}
                        loading={loading}
                    />
                </LibModal>
            ) : null}

            {modalType === 'notif_email' ? (
                <LibModal
                    title={modalData ? 'Update Notification Email' : 'Add Notification Email'}
                    subTitle=""
                    className="!w-[400px]"
                    setOpen={closeModal}
                >
                    <ModalNotificationEmail
                        data={modalData}
                        closeModal={closeModal}
                        handleReload={handleReload}
                    />
                </LibModal>
            ) : null}
            {modalType === 'import-local' ? (
                <LibModal
                    title="Import Configuration (Local)"
                    subTitle=""
                    className="!w-[400px]"
                    setOpen={closeModal}
                >
                    <ModalImportLocalJson
                        path=""
                        selectedPath={selectedPath}
                        setSelectedPath={setSelectedPath}
                        cancelClick={closeModal}
                        onClick={handleLocalJsonImport}
                        loading={loading}
                    />
                </LibModal>
            ) : null}

            {modalType === 'import-cloud' ? (
                <LibModal
                    title="Import Configuration"
                    subTitle=""
                    className="!w-[400px]"
                    setOpen={closeModal}
                >
                    <ModalImportCloudConfigJson
                        path=""
                        loading={loading}
                        selectedPath={selectedPath}
                        setSelectedPath={setSelectedPath}
                        cancelClick={closeModal}
                        onClick={handleCloudConfigJsonImport}
                    />
                </LibModal>
            ) : null}
        </div>
    );
}

export default ConfigSetupWizard;
