import { FC } from 'react';
import { ImageUploadingIcon } from '../../../utils/SVGIcon';

type TProps = {
    loading: boolean;
    className?: string;
};

const Loading: FC<TProps> = ({ loading, className }) => {
    if (!loading) {
        return null;
    }
    return (
        <div className="absolute top-0 left-0 w-full h-full flex justify-center items-center z-40 rounded-15">
            <ImageUploadingIcon className={className} />
        </div>
    );
};

export default Loading;
