import { useEffect, useState } from 'react';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { ButtonPrimary } from '../../../../utils/Button';
import electron from '../../../../utils/electron';
import { IconDone, IconInformationLine } from '../../../../utils/SVGIcon';

interface SuccessProps {
    closeModal: () => void;
}

interface FailedProps {
    data: any;
    closeModal: () => void;
    openModal: (type: string) => void;
    os: 'windows' | 'linux' | 'mac' | 'unknown';
}

interface ContentProps {
    data: any;
    success: boolean;
    dumpType: string;
    openModal: (type: string) => void;
    closeModal: () => void;
    os: 'windows' | 'linux' | 'mac' | 'unknown';
}

interface ModalTestDumpPathProps {
    data: any;
    openModal: (type: string) => void;
    closeModal: () => void;
}

function Testing(): JSX.Element {
    return (
        <div className="h-40 w-full flex items-center justify-center">
            <div className="text-center text-2xl font-normal opacity-85 loading-dots">Testing</div>
        </div>
    );
}

function Success({ closeModal }: SuccessProps): JSX.Element {
    return (
        <div className="h-40 w-full flex flex-col justify-evenly items-center">
            {/* <div>Found Path: &quot;C:\Users\user\Documents\dump.sql&quot;</div> */}

            <div className="flex items-center gap-2">
                <span className="text-center text-xl opacity-95">Test Success</span>{' '}
                <IconDone className="w-5" />
            </div>

            <div className="">
                <ButtonPrimary
                    title="Ok"
                    onClick={closeModal}
                    className="[&>span]:text-lg"
                    disabled={false}
                />
            </div>
        </div>
    );
}

function Failed({ data, closeModal, openModal, os }: FailedProps): JSX.Element {
    //return sqlType according to data.type check if data.typ includes 'mysql' or 'pgsql' or 'mssql'
    const getSqlType = () => {
        const type = data.type?.toLowerCase() || {};
        if (type.includes('mysql')) {
            return 'modal_mysql_instruction';
        } else if (type.includes('pgsql')) {
            return 'modal_pgsql_instruction';
        } else if (type.includes('mssql')) {
            return 'modal_mssql_instruction';
        }
        return null;
    };

    return (
        <div className="h-40 w-full flex flex-col justify-evenly items-center">
            <div className="text-center">
                Failed to find dump path. Please check if the environment is set correctly or SQL is
                installed.
            </div>

            {os === 'windows' && (
                <div className="flex items-center gap-2">
                    <button
                        type="button"
                        onClick={() => electron()?.openEnvVariablesDialog()}
                        className="text-sm text-secondary"
                    >
                        Open Environment Settings
                    </button>
                    <button
                        type="button"
                        onClick={() => openModal(getSqlType() || '')}
                        className="w-4"
                    >
                        <IconInformationLine className="" />
                    </button>
                </div>
            )}

            <div>
                <ButtonPrimary title="ok" onClick={closeModal} disabled={false} />
            </div>
        </div>
    );
}

function Content({ data, success, openModal, closeModal, os }: ContentProps): JSX.Element {
    if (success) {
        return <Success closeModal={closeModal} />;
    } else {
        return <Failed data={data} closeModal={closeModal} openModal={openModal} os={os} />;
    }
}

function ModalTestDumpPath({ data, openModal, closeModal }: ModalTestDumpPathProps): JSX.Element {
    const [os, setOs] = useState<'windows' | 'linux' | 'mac' | 'unknown'>('unknown');
    const [loading, setLoading] = useState({
        loading: false,
        success: false
    });

    const { setMessage } = useTemplate();

    const { path, type } = data || {};

    useEffect(() => {
        setLoading((prev) => ({ ...prev, loading: true }));

        window.electronAPI
            ?.testDumpPath({
                path,
                dumpType: type
            })
            .then(({ error, message }) => {
                // console.log(message);
                // -- Set message
                setMessage(message || '');
                if (error === 0) {
                    // console.log('ok');
                    // -- loading false if success
                    setLoading((prev) => ({ ...prev, loading: false, success: true }));
                } else {
                    // console.log('not ok');
                    // -- loading false if error
                    setLoading((prev) => ({ ...prev, loading: false, success: false }));
                }
            })
            .catch((err) => {
                // -- loading false if error
                setLoading((prev) => ({ ...prev, loading: false, success: false }));

                // -- Set error message
                setMessage(err?.toString());
                // console.log(err);
            });
    }, [path, setMessage, type]);

    useEffect(() => {
        window.electronAPI
            ?.checkOs()
            .then((os) => {
                setOs(os);
                // -- Set OS in template context if needed
            })
            .catch((err) => {
                // -- Handle error if needed
                console.error('Error checking OS:', err);
            });
    }, []);
    // console.log(window.electronAPI?.checkOs(), 'OS Check+++++++++++');

    if (loading.loading) {
        return <Testing />;
    }
    return (
        <Content
            data={data}
            success={loading.success}
            dumpType={type}
            openModal={openModal}
            closeModal={closeModal}
            os={os}
        />
    );
}

export default ModalTestDumpPath;
