/* eslint-disable no-underscore-dangle */
import { useState } from 'react';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { LogData } from '../../../../data/types';
import cn from '../../../../utils/cn';
import { GetDate, GetTime } from '../../../../utils/date';
import formatBytes from '../../../../utils/formatBytes';
import { DownloadIcon, IconActivityLine, RemoveIcon } from '../../../../utils/SVGIcon';

interface LogItemProps {
    data: LogData;
    reloadWithData: () => void;
}

interface ApiResponse {
    error: number;
    message?: string;
    data?: any;
}

function LogItem({ data, reloadWithData }: LogItemProps): JSX.Element {
    const [loading, setLoading] = useState<boolean>(false);
    const { setMessage, setConfirm, setTemplateLoading } = useTemplate();

    const handleDownloadLog = (): void => {
        window.electronAPI
            ?.openDirectoryDialog()
            .then((res: string) => {
                if (res) {
                    setLoading(true);
                    // -- if directory selected then download log file
                    window.electronAPI
                        ?.downloadLogFile({
                            file: data?.file,
                            dirPath: res
                        })
                        .then(({ error, message }: ApiResponse) => {
                            setLoading(false);
                            setMessage(message || '');
                            if (error === 0) {
                                setLoading(false);
                            }
                        })
                        .catch((err) => {
                            setLoading(false);
                            setMessage(err?.toString());
                        });
                }
            })
            .catch((err) => {
                setLoading(false);
                setMessage(err?.toString());
            });
    };

    const handelRemoveLog = (): void => {
        setConfirm({
            text: `Are you sure you want to remove this log?`,
            textAction: 'Yes',
            clickAction: () => {
                // -- Confirm button loading true
                setTemplateLoading(true);
                window.electronAPI
                    ?.deleteLogFile({
                        file: data?.file || ''
                    })
                    .then(({ error, message }: ApiResponse) => {
                        setMessage(message || '');
                        // -- Confirm button loading false
                        setTemplateLoading(false);
                        if (error === 0) {
                            setConfirm(null);
                            reloadWithData();
                        }
                    })
                    .catch((err: any) => {
                        setMessage(err?.toString());
                        // -- Confirm button loading false
                        setTemplateLoading(false);
                    });
            }
        });
    };

    return (
        <div
            className={cn(`list_item`, {
                'placeholder-mode': data?.demo
            })}
        >
            <div className="bg-secondary-50 rounded-10">
                <IconActivityLine className="" />
            </div>
            <div>
                <div className="text-sm text-primary font-normal capitalize flex items-center gap-x-1">
                    <span className=" font-medium normal-case">{data?.name}</span>
                    <span>&bull;</span>
                    <span>{GetDate(data?.created)}</span>
                    <span>&bull;</span>
                    <span>{GetTime(data?.created)}</span>
                </div>
                <div className="text-xs text-primary-700 ">
                    <span>Size: {formatBytes(data?.size)}</span>
                </div>
            </div>
            <div className="flex gap-x-2">
                <button
                    type="button"
                    onClick={handelRemoveLog}
                    className="group bg-[#FFDFDF] hover:bg-error flex items-center justify-center w-11 h-11 !rounded-10 p-[8px]"
                >
                    <RemoveIcon className="text-error group-hover:text-white rounded-10" />
                </button>
                <button
                    type="button"
                    onClick={!loading ? handleDownloadLog : undefined}
                    className="!rounded-10 bg-secondary flex items-center justify-center w-11 h-11 p-[8px]"
                >
                    <DownloadIcon className="rounded-10" animate={loading} />
                </button>
            </div>
        </div>
    );
}

export default LogItem;
