/* eslint-disable no-underscore-dangle */
import { useEffect } from 'react';
import { useApp } from '../../../../context/AppProvider';
import { useInit } from '../../../../context/InitProvider';
import { UploadingIcon, WarningIcon } from '../../../../utils/SVGIcon';
import cn from '../../../../utils/cn';
import CountdownTimer from '../../../../utils/countDownTimer';
import exploreDirectory from '../../../../utils/exploreDirectory';
import { iconHandler, sourceBgHandler } from '../../../../utils/sourceIcon';
import { useConfig } from '../../Config/ConfigProvider';
import { useSource } from '../SourceProvider';
import SourceExpandedInfo from './SourceExpandedInfo';
import SourceManageOptions from './SourceManageOptions';

function SourceBasicInfo({
    data,
    task,
    handleDeleteSource,
    handleForceBackup,
    handleCleanupBackup,
    openModal,
    isExpand
}) {
    const { backupItem } = useInit();

    const { setReloadKey } = useSource();

    // check if backup is running
    const isUploading = backupItem[data?._id] === 'running';

    // check if backup is done
    const isDone = backupItem[data?._id] === 'done';

    // task status
    const isRunning = task?.running;

    // source type directory or not
    const isDirectory = data?.type === 'directory';

    const explorable = isExpand && isDirectory;

    // update data without showing demo data when backup is done
    useEffect(() => {
        if (isDone) {
            setReloadKey((prev) => prev + 1);
        }
    }, [isDone, setReloadKey]);

    return (
        <div
            className={cn(`list_item`, {
                'placeholder-mode': data?.demo
            })}
        >
            <div className={`${sourceBgHandler(data?.type)} rounded-10`}>
                {isUploading ? <UploadingIcon /> : null}
                {!isUploading && iconHandler(data?.type)}
            </div>

            <div className="!w-[calc(100%_-_250px)] ">
                <div className="text-sm text-primary font-medium capitalize flex items-center gap-x-1">
                    <span>{data?.type}</span>

                    {data?.errorStatus && (
                        <button
                            type="button"
                            data-tooltip-id="myToolTip"
                            data-tooltip-content={data?.errorMessage}
                            className="inline-flex size-4 rounded-full bg-warning text-white"
                        >
                            <WarningIcon />
                        </button>
                    )}
                    <span
                        className={cn({
                            'running-circle': isRunning,
                            'stop-circle': !isRunning
                        })}
                    />
                    <CountdownTimer
                        show={isRunning}
                        key={task?.timeRemaining}
                        nextRun={task?.nextRun}
                    />
                </div>
                <button
                    type="button"
                    data-tooltip-id="myToolTip"
                    data-tooltip-content={explorable ? 'explore directory' : ''}
                    className={cn(`text-xs text-primary-700 inline`, {
                        'underline hover:text-secondary': explorable
                    })}
                    onClick={
                        explorable
                            ? (ev) => ev.stopPropagation() + exploreDirectory(data?.databaseOrPath)
                            : null
                    }
                >
                    <span>{data?.databaseOrPath}</span>
                </button>
            </div>

            <SourceManageOptions
                data={data}
                isUploading={isUploading}
                handleDeleteSource={handleDeleteSource}
                handleForceBackup={handleForceBackup}
                handleCleanupBackup={handleCleanupBackup}
                openModal={openModal}
            />
        </div>
    );
}

function SourceItem({ data, selectedSource, setSelectedSource }) {
    const { openModal } = useApp();

    const { destinationData } = useConfig();

    const {
        taskSt,
        handleDeleteSource,
        handleUpdateAutoStart,
        handleStartBackup,
        handleStopBackup,
        handleForceBackup,
        handleCleanupBackup
    } = useSource();

    const handleSelectSource = () => {
        // if one is selected and another clicked for select then previous one will be unselected
        if (selectedSource?._id === data?._id) {
            setSelectedSource({ _id: 0 });
        } else {
            setSelectedSource(data);
        }
    };

    const task = taskSt?.find((t) => t.id === data?._id);
    const isExpand = selectedSource?._id === data?._id;

    return (
        <div
            role="button"
            tabIndex={-1}
            className={cn(`list_container `, {
                active: selectedSource?._id === data?._id
            })}
            onClick={handleSelectSource}
        >
            <SourceBasicInfo
                data={data}
                task={task}
                handleDeleteSource={handleDeleteSource}
                handleForceBackup={handleForceBackup}
                handleCleanupBackup={handleCleanupBackup}
                openModal={openModal}
                isExpand={isExpand}
            />

            <SourceExpandedInfo
                data={data}
                task={task}
                destinationData={destinationData}
                handleStartBackup={handleStartBackup}
                handleStopBackup={handleStopBackup}
                closeClick={handleSelectSource}
                handleUpdateAutoStart={handleUpdateAutoStart}
            />
        </div>
    );
}

export default SourceItem;
