/* eslint-disable no-underscore-dangle */
import { useState } from 'react';
import SectionHeader from '../../Features/SectionHeader/SectionHeader';
import SourceItem from './Components/SourceItem';
import TitleArea from './Components/TitleArea';
import SourceModals from './Modals/SourceModals';
import SourceProvider, { useSource } from './SourceProvider';
import { SelectedSource } from './types';

function Content() {
    const { sourcesData, handleReload } = useSource();
    // -- State to store selected source
    const [selectedSource, setSelectedSource] = useState<SelectedSource>({ _id: 0 });

    return (
        <div className="w-full h-full">
            <SectionHeader className="h-21">
                <TitleArea handleReload={handleReload} />
            </SectionHeader>
            <div className="w-full h-[calc(100%_-_96px)] overflow-auto p-5 pb-44">
                {sourcesData && sourcesData.length === 0 && (
                    <div className="w-full h-full flex justify-center items-center">
                        <p className="text-lg text-gray-400">No sources found</p>
                    </div>
                )}

                {sourcesData?.map((source) => (
                    <SourceItem
                        key={source._id}
                        data={source}
                        selectedSource={selectedSource}
                        setSelectedSource={setSelectedSource}
                    />
                ))}
            </div>

            {/* all modals related to source */}
            <SourceModals />
        </div>
    );
}

function SourceSection() {
    return (
        <SourceProvider>
            <Content />
        </SourceProvider>
    );
}

export default SourceSection;
