import { useState } from 'react';
import { OptionButton } from '../../../utils/Button';
import InstOption from '../../../utils/InstOption';

interface WizardHeaderProps {
    openModal: (type: string) => void;
}

function WizardHeader({ openModal }: WizardHeaderProps): JSX.Element {
    const [showMenu, setShowMenu] = useState<boolean>(false);

    return (
        <div className="flex items-center justify-between border-b border-primary-100 h-13 px-5">
            <div className="text-lg font-medium">Configurations</div>

            {/* <ButtonPrimary
                title="Import Configuration"
                className="!h-9"
                onClick={() => openModal('import-config')}
            /> */}
            <div className="relative">
                <OptionButton className="size-9" onClick={() => setShowMenu(true)} />
                <InstOption
                    showMenu={showMenu}
                    setShowMenu={setShowMenu}
                    className="top-[53px] -right-[10px]"
                >
                    <button type="button" onClick={() => openModal('import-local')}>
                        Import (Local)
                    </button>
                    <button type="button" onClick={() => openModal('import-cloud')}>
                        Import (Cloud)
                    </button>
                </InstOption>
            </div>
        </div>
    );
}

export default WizardHeader;
