import { useMemo } from 'react';
import { useApp } from '../../../context/AppProvider';
import { useTemplate } from '../../../context/template/TemplateProvider';
import {
    OperationBackupCleanup,
    OperationBackupStart,
    OperationBackupStop,
    OperationCreateSource,
    OperationDeleteSource,
    OperationForceBackup,
    OperationUpdateAutoStart,
    OperationUpdateFrequency,
    OperationUpdateSource
} from './SourceOperation';

const useSourceOperationHandler = (
    handleReload,
    setReloadKey,
    setSelectedPath,
    setLoading,
    setForceBackupLoading
) => {
    const { setMessage, setTemplateLoading, setConfirm } = useTemplate();
    const { closeModal } = useApp();

    const value = useMemo(() => {
        const handelCreateSource = (ev, data) => {
            setLoading(true);
            if (ev) {
                ev.preventDefault();
            }

            OperationCreateSource(data)
                .then(({ message }) => {
                    // -- loading false if success
                    setLoading(false);
                    // -- Set message
                    setMessage(message);
                    // -- Close modal
                    closeModal();

                    // -- Reset selected path
                    setSelectedPath('');

                    // -- Reload sources data
                    handleReload();
                })
                .catch((err) => {
                    // -- loading false if error
                    setLoading(false);

                    // -- Set error message
                    setMessage(err?.toString());
                });
        };

        // --Handler to update source
        const handleUpdateSource = (ev, data) => {
            setLoading(true);
            if (ev) {
                ev.preventDefault();
            }

            OperationUpdateSource(data)
                .then(({ message }) => {
                    // -- loading false if success
                    setLoading(false);

                    // -- Set message
                    setMessage(message);
                    // -- Close modal
                    closeModal();
                    // -- Reset selected path
                    setSelectedPath('');

                    // -- Reload sources data
                    handleReload();
                })
                .catch((err) => {
                    // -- loading false if error
                    setLoading(false);

                    // -- Set error message
                    setMessage(err?.toString());
                    console.log(err);
                });
        };

        const autostartHandler = (data) => {
            setTemplateLoading(true);
            OperationUpdateAutoStart(data)
                .then(({ message }) => {
                    // -- loading false if success
                    setLoading(false);

                    // -- Set message
                    setMessage(message);

                    setTemplateLoading(false);

                    // -- Close modal
                    closeModal();

                    // -- Reload sources data
                    setReloadKey((prev) => prev + 1);

                    // -- close confirm dialog
                    setConfirm(null);
                })
                .catch((err) => {
                    setTemplateLoading(false);

                    // -- loading false if error
                    setLoading(false);

                    // -- Set error message
                    setMessage(err?.toString());
                    console.log(err);
                });
        };

        // --Handler to update auto start
        const handleUpdateAutoStart = (ev, data) => {
            setLoading(true);
            if (ev) {
                ev.preventDefault();
            }

            // -- confirm dialog when disable auto start
            if (data.autostart) {
                autostartHandler(data);
            } else {
                setConfirm({
                    text: `Are you sure you want to disable auto start for this source?`,
                    textAction: 'Yes',
                    clickAction: () => {
                        autostartHandler(data);
                    }
                });
            }
        };

        // --Handler to update frequency
        const handleUpdateFrequency = (ev, data) => {
            setLoading(true);
            if (ev) {
                ev.preventDefault();
            }

            OperationUpdateFrequency(data)
                .then(({ message }) => {
                    // -- loading false if success
                    setLoading(false);

                    // -- Set message
                    setMessage(message);
                    // -- Close modal
                    closeModal();

                    // -- Reload sources data
                    handleReload();
                })
                .catch((err) => {
                    // -- loading false if error
                    setLoading(false);

                    // -- Set error message
                    setMessage(err?.toString());
                    console.log(err);
                });
        };

        // --Handler to delete source
        const handleDeleteSource = (data) => {
            setLoading(true);
            setConfirm({
                text: `Are you sure you want to delete this source?`,
                textAction: 'Yes',
                clickAction: () => {
                    OperationDeleteSource(data)
                        .then(({ message }) => {
                            // -- loading false if success
                            setLoading(false);

                            // ---- Set message
                            setMessage(message);

                            // -- Reload sources data
                            handleReload();

                            // -- close confirm dialog
                            setConfirm(null);
                        })
                        .catch((err) => {
                            // -- loading false if error
                            setLoading(false);

                            // -- Set error message
                            setMessage(err?.toString());
                        });
                }
            });
        };

        // --Handler to start backup
        const handleStartBackup = (ev, id) => {
            ev.stopPropagation();
            OperationBackupStart(id)
                .then(({ message }) => {
                    // ---- Set message
                    setMessage(message);
                    // -- Reload task data
                    setReloadKey((prev) => prev + 1);
                })
                .catch((err) => {
                    // -- Set error message
                    setMessage(err?.toString());
                });
        };

        // --Handler to stop backup
        const handleStopBackup = (id) => {
            OperationBackupStop(id)
                .then(({ message }) => {
                    // ---- Set message
                    setMessage(message);
                    // -- Reload task data
                    setReloadKey((prev) => prev + 1);

                    // -- close confirm dialog
                    setConfirm(null);
                })
                .catch((err) => {
                    // -- Set error message
                    setMessage(err?.toString());
                });
        };

        // --Handler to  start force backup
        const handleForceBackup = (ev, data) => {
            ev.stopPropagation();

            setConfirm({
                text: `Are you sure you want to force backup this source?`,
                textAction: 'Yes',
                clickAction: () => {
                    setForceBackupLoading(true);
                    OperationForceBackup(data)
                        .then(({ message }) => {
                            // if (error === 0) {

                            // }

                            // -- Reload data
                            setReloadKey((prev) => prev + 1);

                            // -- close confirm dialog
                            setConfirm(null);

                            // ---- Set message
                            setMessage(message);

                            // -- loading false if success
                            setForceBackupLoading(false);
                        })
                        .catch((err) => {
                            // -- close confirm dialog
                            setConfirm(null);

                            // -- loading false if error
                            setForceBackupLoading(false);

                            // -- Reload data
                            setReloadKey((prev) => prev + 1);

                            // -- Set error message
                            setMessage(err?.toString());
                        });
                }
            });
        };

        // --Handler to  start force backup
        const handleCleanupBackup = (ev, item) => {
            ev.stopPropagation();

            setConfirm({
                text: `Are you sure you want to cleanup backups for this source?`,
                textAction: 'Yes',
                clickAction: () => {
                    setTemplateLoading(true);
                    OperationBackupCleanup(item)
                        .then(({ message }) => {
                            // -- Reload task data
                            setReloadKey((prev) => prev + 1);

                            // -- close confirm dialog
                            setConfirm(null);
                            // ---- Set message
                            setTemplateLoading(false);
                            setMessage(message);
                        })
                        .catch((err) => {
                            setTemplateLoading(false);
                            // -- Set error message
                            setMessage(err?.toString());
                        });
                }
            });
        };

        return {
            handelCreateSource,
            handleUpdateSource,
            handleUpdateAutoStart,
            handleUpdateFrequency,
            handleDeleteSource,
            handleStartBackup,
            handleStopBackup,
            handleForceBackup,
            handleCleanupBackup
        };
    }, [
        closeModal,
        handleReload,
        setConfirm,
        setForceBackupLoading,
        setLoading,
        setMessage,
        setReloadKey,
        setSelectedPath,
        setTemplateLoading
    ]);

    return value;
};

export default useSourceOperationHandler;
