//make a hook that export handlers and takes handlers required data or states from props on parent

import { useMemo } from 'react';
import { useApp } from '../../../context/AppProvider';
import {
    OperationCloudConfigJsonImport,
    OperationConfigExport,
    OperationLocalJsonImport,
    OperationSaveDir,
    OperationSetDumpPath
} from './ConfigOperation';

// Define types for the hook parameters
interface ConfigData {
    [key: string]: any;
}

interface MessageType {
    text?: string;
    directory?: string;
    timeout?: number;
    [key: string]: any;
}

// Define types for operation responses (matching ConfigOperation.ts)
interface OperationResponse {
    error: number;
    message?: string;
    data?: any;
}

interface UseConfigOperationHandlerReturn {
    handleSaveDirectory: () => void;
    handleLocalJsonImport: () => void;
    handleCloudConfigJsonImport: () => void;
    handleConfigExport: (ev: React.MouseEvent) => void;
    handleSetDumpPath: (type: string, path: string, modal?: string) => void;
}

const useConfigOperationHandler = (
    handleReload: () => void,
    handleCancelClick: () => void,
    selectedPath: string,
    setSelectedPath: (path: string) => void,
    configsData: ConfigData,
    setLoading: (loading: boolean) => void,
    setMessage: (message: string | MessageType) => void,
    appId: string | number
): UseConfigOperationHandlerReturn => {
    const { closeModal } = useApp();
    const value = useMemo((): UseConfigOperationHandlerReturn => {
        const handleSaveDirectory = (): void => {
            // -- Create payload
            const payload = {
                ...configsData,
                directory: selectedPath
            };

            // -- Set loading status to true while adding or updating default directory
            setLoading(true);
            OperationSaveDir(payload)
                .then(({ message }: { message?: string } = {}) => {
                    // -- Set message
                    setMessage(message || '');
                    // -- Set loading status to false after adding or updating default directory
                    setLoading(false);

                    // --close modal
                    closeModal();

                    // -- Reset selected path
                    setSelectedPath('');
                    // -- Reload default directory data
                    handleReload();
                })
                .catch((err: any) => {
                    // -- Set loading status to false after adding or updating default directory
                    setLoading(false);
                    // -- Set error message
                    setMessage(err?.toString());
                });
        };

        // -- Local JSON import handler
        const handleLocalJsonImport = (): void => {
            setLoading(true);
            OperationLocalJsonImport({ configPath: selectedPath })
                .then(({ message }: OperationResponse) => {
                    setMessage(message || '');
                    setLoading(false);
                    closeModal();
                    setSelectedPath('');
                    handleReload();
                })
                .catch((err: any) => {
                    setLoading(false);
                    setMessage(err?.toString());
                });
        };

        // -- Cloud config JSON import handler
        const handleCloudConfigJsonImport = (): void => {
            setLoading(true);
            OperationCloudConfigJsonImport(selectedPath)
                .then(({ error, message }: OperationResponse) => {
                    setMessage(message || '');
                    setLoading(false);
                    closeModal();
                    setSelectedPath('');
                    handleReload();
                })
                .catch((err: any) => {
                    setLoading(false);
                    setMessage(err?.toString());
                });
        };

        // -- Config export handler
        const handleConfigExport = (ev: React.MouseEvent): void => {
            ev.stopPropagation();
            setLoading(true);
            OperationConfigExport({
                appId,
                savedPath: selectedPath
            })
                .then(({ error, message }: OperationResponse) => {
                    // setMessage(message);
                    setMessage({ text: message || '', directory: selectedPath, timeout: 20000 });
                    handleCancelClick();
                    setLoading(false);
                })
                .catch((err: any) => {
                    // -- Set error message
                    setMessage(err?.toString());
                    setLoading(false);
                });
        };

        // -- Set dump path handler
        const handleSetDumpPath = (type: string, path: string, modal: string = 'true'): void => {
            OperationSetDumpPath({
                path,
                dumpType: type
            })
                .then(({ error, message }: OperationResponse) => {
                    // -- Set message
                    setMessage(message || '');
                    // -- loading false if success
                    setLoading(false);
                    if (modal === 'true') {
                        closeModal();
                    }

                    // -- Reload configs data
                    handleReload();
                })
                .catch((err: any) => {
                    // -- loading false if error
                    setLoading(false);

                    // -- Set error message
                    setMessage(err?.toString());
                });
        };

        return {
            handleSaveDirectory,
            handleLocalJsonImport,
            handleCloudConfigJsonImport,
            handleConfigExport,
            handleSetDumpPath
        };
    }, [
        appId,
        closeModal,
        configsData,
        handleCancelClick,
        handleReload,
        selectedPath,
        setLoading,
        setMessage,
        setSelectedPath
    ]);

    return value;
};

export default useConfigOperationHandler;
