import { ReactNode } from 'react';
import { EditIcon, MailIcon, PlusIcon } from '../../../../utils/SVGIcon';
import cn from '../../../../utils/cn';

interface Col2Props {
    title: string;
    value: string | ReactNode;
}

function Col2({ title, value }: Col2Props) {
    return (
        <div className="flex justify-between items-center">
            <div>
                <h2 className="text-sm text-primary font-medium capitalize">
                    <span>{title}</span>
                </h2>
                <p className="text-xs text-primary-700">
                    <span>{value}</span>
                </p>
            </div>
        </div>
    );
}

interface SMTPData {
    default?: boolean;
    username?: string;
    hostname?: string;
    [key: string]: any;
}

interface ConfigSMTPProps {
    data?: SMTPData;
    openModal: (type: string, data?: SMTPData) => void;
}

function ConfigSMTP({ data = {}, openModal }: ConfigSMTPProps) {
    return (
        <div className={cn('list_item')}>
            <div className="bg-[#08C456]/10 text-[#08C456] rounded-10">
                <MailIcon className="" />
            </div>

            <div>
                <Col2 title="SMTP Config" value={data?.default ? 'Default' : data?.username} />
            </div>

            <div className={cn(`flex items-center gap-x-4`)}>
                {data?.hostname && (
                    <button
                        type="button"
                        className="!inline-block !h-auto !w-12 bg-secondary-100 hover:bg-secondary rounded-5 px-2 py-1 text-sm text-secondary hover:text-white"
                        onClick={() => openModal('test-smtp-config', data)}
                    >
                        Test
                    </button>
                )}
                <button
                    type="button"
                    onClick={() => openModal('set-smtp-config', data)}
                    className="bg-secondary-100 text-secondary rounded-lg p-1"
                >
                    {Object.keys(data).length ? (
                        <EditIcon className="" />
                    ) : (
                        <PlusIcon className="" />
                    )}
                </button>
            </div>
        </div>
    );
}

export default ConfigSMTP;
