import { useEffect, useState } from 'react';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { ButtonCancel, ButtonPrimary } from '../../../../utils/Button';
import { MagnifyIcon } from '../../../../utils/SVGIcon';

interface SuccessProps {
    closeModal: () => void;
    dumpType: string;
    path: string;
    handleSetDumpPath: (type: string, path: string) => void;
}

interface FailedProps {
    closeModal: () => void;
}

interface ContentProps {
    dumpType: string;
    operation: {
        loading: boolean;
        success: boolean;
        path: string;
    };
    closeModal: () => void;
    handleSetDumpPath: (type: string, path: string) => void;
}

interface ModalScanDumpFileProps {
    data: {
        path: string;
        type: string;
    };
    closeModal: () => void;
    handleSetDumpPath: (type: string, path: string) => void;
}

function Scanning(): JSX.Element {
    return (
        <div className="h-50 w-full">
            <div className="relative h-36">
                <MagnifyIcon className="text-secondary" />
            </div>

            <div className="h-[calc(100%_-_144px)] w-full text-center text-lg">Scanning.....</div>
        </div>
    );
}

function Success({ closeModal, dumpType, path, handleSetDumpPath }: SuccessProps): JSX.Element {
    return (
        <div className="h-50 w-full flex flex-col justify-evenly items-center">
            {/* <div>Found Path: &quot;C:\Users\user\Documents\dump.sql&quot;</div> */}

            <div>
                <div className="text-center">Found Dump Path: &quot;{path || '--'}&quot;</div>
            </div>

            <div className="flex gap-x-2">
                <ButtonCancel title="Cancel" onClick={closeModal} disabled={false}>
                    Cancel
                </ButtonCancel>
                <ButtonPrimary
                    title={`Set as ${dumpType} dump path`}
                    onClick={() => handleSetDumpPath(dumpType, path)}
                    disabled={false}
                />
            </div>
        </div>
    );
}

function Failed({ closeModal }: FailedProps): JSX.Element {
    return (
        <div className="h-50 w-full flex flex-col justify-evenly items-center">
            <div className="text-center">
                {/* show message that environment maybe not set or sql not installed */}
                Failed to find dump path. Please check if the environment is set correctly or SQL is
                installed.
            </div>

            <div>
                <ButtonPrimary title="ok" onClick={closeModal} disabled={false} />
            </div>
        </div>
    );
}

function Content({
    dumpType,
    operation,
    closeModal,
    handleSetDumpPath
}: ContentProps): JSX.Element {
    // const dumpType = modalData?.type.replace(/dump/g, '');
    const { path, success } = operation;

    if (success) {
        return (
            <Success
                closeModal={closeModal}
                path={path}
                dumpType={dumpType}
                handleSetDumpPath={handleSetDumpPath}
            />
        );
    }

    return <Failed closeModal={closeModal} />;
}

function ModalScanDumpFile({
    data,
    closeModal,
    handleSetDumpPath
}: ModalScanDumpFileProps): JSX.Element {
    const [operation, setOperation] = useState({
        loading: false,
        success: false,
        path: ''
    });
    const { setMessage } = useTemplate();

    const { path, type: dumpType } = data || {};

    useEffect(() => {
        setOperation((prev) => ({ ...prev, loading: true }));
        window.electronAPI
            ?.scanDumpPath({
                path,
                dumpType
            })
            .then(
                (
                    { error, message, data: resData } = {
                        error: 1,
                        message: 'API not available',
                        data: null
                    }
                ) => {
                    // -- Set message
                    setMessage(message || '');
                    if (error === 0) {
                        // console.log('ok');
                        // -- loading false if success
                        setOperation((prev) => ({
                            ...prev,
                            loading: false,
                            success: true,
                            path: resData?.path || ''
                        }));
                    } else {
                        // console.log('not ok');
                        // -- loading false if error
                        setOperation((prev) => ({ ...prev, loading: false, success: false }));
                    }
                }
            )
            .catch((err) => {
                // -- loading false if error
                setOperation((prev) => ({ ...prev, loading: false, success: false }));

                // -- Set error message
                setMessage(err?.toString());
                // console.log(err);
            });
    }, [dumpType, path, setMessage]);

    if (operation.loading) {
        return <Scanning />;
    }
    return (
        <Content
            operation={operation}
            dumpType={dumpType}
            closeModal={closeModal}
            handleSetDumpPath={handleSetDumpPath}
        />
    );
}

export default ModalScanDumpFile;
