import { ChangeEvent, useState } from 'react';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { ButtonPrimary } from '../../../../utils/Button';
import { addOption } from '../../../../utils/InputField';
import SelectOption from '../../../../utils/SelectOption';
import { useConfig } from '../../Config/ConfigProvider';
import { BackupSyncFormData } from '../types';

interface BackupSyncModalProps {
    reloadWithData: () => void;
    closeModal: () => void;
}

function BackupSyncModal({ reloadWithData, closeModal }: BackupSyncModalProps) {
    const [formData, setFormData] = useState<BackupSyncFormData>({ cloudName: '' });
    const [loading, setLoading] = useState<boolean>(false);

    const { setMessage } = useTemplate();
    const { appId, destinationData } = useConfig();

    const onChange = (ev: ChangeEvent<HTMLSelectElement>) => {
        if (ev) {
            setFormData((prev) => ({ ...prev, [ev.target.name]: ev.target.value }));
        }
    };

    const handleSyncClick = () => {
        const payload = {
            appId,
            ...formData
        };
        setLoading(true);
        window.electronAPI
            ?.syncBackup(payload)
            .then(({ error, message }) => {
                setMessage(message || 'Sync completed');

                // -- loading false
                setLoading(false);

                if (error === 0) {
                    reloadWithData();
                    closeModal();
                }
            })
            .catch((err: any) => {
                setMessage(err?.toString());

                // -- loading false
                setLoading(false);
            });
    };

    return (
        <div className="space-y-3">
            <SelectOption
                label="Cloud Name"
                name="cloudName"
                placeholder="cloud"
                formData={formData}
                onChange={onChange}
                options={destinationData?.map((data) => addOption(data._id, data.type, data.type))}
            />

            <div className="flex justify-end w-full">
                <ButtonPrimary
                    title="Sync"
                    loading={loading}
                    onClick={handleSyncClick}
                    disabled={false}
                />
            </div>
        </div>
    );
}

export default BackupSyncModal;
