import { useEffect } from 'react';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { ButtonCancel, ButtonPrimary } from '../../../../utils/Button';
import { IconSourceLine } from '../../../../utils/SVGIcon';
import exploreDirectory from '../../../../utils/exploreDirectory';
import truncateString from '../../../../utils/truncateString';
import DirectoryButton from '../../../Features/Directory/Comps/DirectoryButton';
import Loading from '../../../Features/Loading/Loading';

interface ModalActionProps {
    show: boolean;
    handleCancelClick: () => void;
    onClick: (ev: MouseEvent) => void;
    disabled: boolean;
}

function ModalAction({
    show,
    handleCancelClick,
    onClick,
    disabled
}: ModalActionProps): JSX.Element | null {
    // const modifiedOnClick = () => {
    // onClick();
    //     handleCancelClick();
    // };

    if (!show) return null;
    return (
        <div className="flex items-center justify-end gap-2">
            <ButtonCancel
                title="Cancel"
                onClick={handleCancelClick}
                className="!h-10 min-w-20 !text-base !rounded-[8px]"
                disabled={false}
            >
                Cancel
            </ButtonCancel>
            <ButtonPrimary
                title="Export"
                onClick={onClick}
                disabled={disabled}
                className="h-10 min-w-20 [&>span]:!text-base !rounded-[8px]"
            />
        </div>
    );
}

interface ModalExportConfigJsonProps {
    path: string;
    selectedPath: string;
    staticPath: string;
    setSelectedPath: (path: string) => void;
    cancelClick: () => void;
    loading: boolean;
    onClick: (ev: MouseEvent) => void;
}

function ModalExportConfigJson({
    path,
    selectedPath,
    staticPath,
    setSelectedPath,
    cancelClick,
    loading,
    onClick
}: ModalExportConfigJsonProps): JSX.Element {
    const { setMessage } = useTemplate();

    const handleFileBrowse = () => {
        window.electronAPI
            ?.openDirectoryDialog()
            .then((res) => {
                setSelectedPath(res);
            })
            .catch((err) => {
                setMessage(err?.toString(0) || 'Error while browsing file');
            });
    };

    useEffect(() => {
        if (path?.length) {
            setSelectedPath(path);
        }
    }, [path, setSelectedPath]);

    return (
        <div className="w-full h-full p-2 relative">
            <Loading loading={loading} className="" />
            <div className="flex items-center justify-between gap-x-2 mb-8">
                <div className="flex items-start gap-x-3">
                    <div className="w-[50px] h-[50px] flex items-center justify-center p-[2.5] bg-secondary-100 rounded-10">
                        <IconSourceLine className="w-10 h-10 text-secondary" />
                    </div>

                    <div className="text-primary font-medium">
                        <span className="block leading-none">Selected Directory :</span>
                        <span className="text-primary-700 text-xs">
                            {selectedPath ? (
                                <span
                                    className="text-secondary underline"
                                    role="button"
                                    tabIndex={-1}
                                    onClick={() => exploreDirectory(selectedPath)}
                                >
                                    {truncateString(
                                        `${selectedPath} ${staticPath?.length ? `/${staticPath}` : ''}`
                                    )}
                                </span>
                            ) : (
                                'No Directory found'
                            )}
                        </span>
                    </div>
                </div>
                <div>
                    <DirectoryButton
                        title={selectedPath ? 'Edit' : 'Add'}
                        onClick={handleFileBrowse}
                    />
                </div>
            </div>

            <ModalAction
                show
                handleCancelClick={cancelClick}
                onClick={onClick}
                disabled={loading || !selectedPath?.length}
            />
        </div>
    );
}

export default ModalExportConfigJson;
