/* eslint-disable no-underscore-dangle */
import { useEffect, useState } from 'react';
import { useApp } from '../../../../context/AppProvider';
import { ButtonPrimary, OptionButton } from '../../../../utils/Button';
import InstOption from '../../../../utils/InstOption';
import { ReloadIcon } from '../../../../utils/SVGIcon';
import cn from '../../../../utils/cn';
import formatBytes from '../../../../utils/formatBytes';
import truncateString from '../../../../utils/truncateString';
import SourceDropdown from '../../../Features/SourceDropdown/SourceDropdown';
import { BackupData, NavigateData, SourceData } from '../types';

interface InfoRowProps {
    title?: string;
    value: string;
    className?: string;
}

interface TitleAreaProps {
    data: BackupData[];
    sourceId: string;
    directory?: string;
    handleReload: () => void;
    onChange: (value: NavigateData) => void;
    formData: NavigateData | null;
}

function InfoRow({ title, value, className }: InfoRowProps) {
    return (
        <div className={cn(`flex items-center gap-x-2 text-sm`, className)}>
            {title && <p className="text-primary-700">{title}:</p>}
            <p className="text-secondary ">
                <span>{value}</span>
            </p>
        </div>
    );
}

function ManageMenu() {
    const [showMenu, setShowMenu] = useState<boolean>(false);

    const { openModal } = useApp();
    return (
        <div className="relative">
            <OptionButton className="size-10" onClick={() => setShowMenu(true)} />

            <InstOption
                showMenu={showMenu}
                setShowMenu={setShowMenu}
                className="w-44 top-[53px] -right-2"
            >
                <button
                    type="button"
                    onClick={(ev) => {
                        ev.stopPropagation();
                        openModal(`sync-backup`);
                    }}
                >
                    Sync with cloud
                </button>
            </InstOption>
        </div>
    );
}

function TitleArea({
    data,
    sourceId,
    directory,
    handleReload,
    onChange,
    formData
}: TitleAreaProps) {
    const [sourceData, setSourcesData] = useState<SourceData[]>([]);

    // Use reduce to sum up the sizes
    const totalBackupSize = data.reduce((total, backup) => {
        const size = typeof backup.size === 'string' ? parseInt(backup.size, 10) || 0 : backup.size;
        return total + size;
    }, 0);

    // Fetch sources data
    useEffect(() => {
        // setSourcesData(sourceDemoData);
        window?.electronAPI
            ?.getSources()
            .then(({ data: source } = {}) => {
                setSourcesData(source || []);
            })
            .catch((err: any) => {
                console.log(err);
                setSourcesData([]);
            });
    }, []);

    return (
        <div className="h-full flex items-center justify-between gap-x-3">
            <div>
                <div className="mb-2 flex items-center gap-x-3">
                    <p className="text-[25px] text-primary font-medium leading-[30px]">Backups:</p>
                    {data && data?.length !== 0 ? (
                        <ButtonPrimary
                            title={`${data?.filter((d) => !d.demo)?.length} | ${formatBytes(totalBackupSize)}`}
                            className="whitespace-nowrap !min-w-[110px] !w-auto !h-9 bg-secondary-200 text-secondary"
                            onClick={null}
                            disabled={false}
                        />
                    ) : null}
                </div>

                <div className="space-y-1">
                    {sourceId === formData?._id && formData?._id !== 'all' && (
                        <InfoRow title="" value={truncateString(directory || '', 20)} />
                    )}
                    {formData?._id && formData?._id !== 'all' && (
                        <InfoRow title="Source ID" value={formData?._id} />
                    )}
                </div>
            </div>

            <div className={cn(`flex items-center justify-end gap-x-2`)}>
                <div className=" w-48 ">
                    <SourceDropdown
                        label="Select Source ID"
                        options={[
                            ...sourceData,
                            {
                                _id: 'all',
                                databaseOrPath: 'all',
                                type: 'all'
                            }
                        ]}
                        onSelect={(option: NavigateData) => onChange(option)}
                        formData={formData}
                    />
                </div>
                <button type="button" onClick={handleReload} className="block !w-10 h-10">
                    <ReloadIcon className="" />
                </button>

                <ManageMenu />
            </div>
        </div>
    );
}

export default TitleArea;
