import { useState } from 'react';
import { useApp } from '../../../context/AppProvider';
import SectionHeader from '../../Features/SectionHeader/SectionHeader';
import ConfigTabBasic from './Components/ConfigTabBasic';
import ConfigTabDumpPath from './Components/ConfigTabDumpPath';
import ConfigTabs from './Components/ConfigTabs';
import TitleArea from './Components/TitleArea';
import ConfigProvider, { useConfig } from './ConfigProvider';
import Modals from './Modals/Modals';

interface SectionBodyProps {
    openModal: (type: string, data?: any) => void;
    activeTab: string;
}

function SectionBody({ openModal, activeTab }: SectionBodyProps) {
    const { configsData } = useConfig();
    return (
        <div className={configsData.demo ? 'placeholder-mode' : ''}>
            {activeTab === 'basic' ? (
                <ConfigTabBasic configsData={configsData} openModal={openModal} />
            ) : null}

            {activeTab === 'dump-path' ? (
                <ConfigTabDumpPath data={configsData} openModal={openModal} />
            ) : null}
        </div>
    );
}

function ConfigSection(): JSX.Element {
    const [activeTab, setActiveTab] = useState<string>('basic');

    const { openModal } = useApp();
    return (
        <ConfigProvider>
            <div className="h-full">
                <SectionHeader className="h-21 flex items-center">
                    <TitleArea />
                </SectionHeader>

                <div className="w-full h-[calc(100%_-_90px)] overflow-auto p-5 pt-2 pb-24">
                    <ConfigTabs tabClick={setActiveTab} activeTab={activeTab} />
                    <SectionBody activeTab={activeTab} openModal={openModal} />

                    {/* modals */}
                    <Modals />
                </div>
            </div>
        </ConfigProvider>
    );
}

export default ConfigSection;
