import React, { useEffect } from 'react';
import { ButtonCancel, ButtonPrimary } from '../../../../utils/Button';
import InputField from '../../../../utils/InputField';
import Loading from '../../../Features/Loading/Loading';

interface ModalActionProps {
    show: boolean;
    handleCancelClick: () => void;
    onClick: () => void;
    disabled: boolean;
}

function ModalAction({
    show,
    handleCancelClick,
    onClick,
    disabled
}: ModalActionProps): JSX.Element | null {
    // const modifiedOnClick = () => {
    // onClick();
    //     handleCancelClick();
    // };

    if (!show) return null;
    return (
        <div className="flex items-center justify-end gap-2">
            <ButtonCancel
                title="Cancel"
                onClick={handleCancelClick}
                className="!h-10 min-w-20 !text-base !rounded-[8px]"
                disabled={false}
            >
                Cancel
            </ButtonCancel>
            <ButtonPrimary
                title="Import"
                onClick={onClick}
                disabled={disabled}
                className="h-10 min-w-20 [&>span]:!text-base !rounded-[8px]"
            />
        </div>
    );
}

interface ModalImportCloudConfigJsonProps {
    path: string;
    selectedPath: string | Record<string, any>;
    setSelectedPath: (value: any) => void;
    onClick: () => void;
    cancelClick: () => void;
    loading: boolean;
}

function ModalImportCloudConfigJson({
    path,
    selectedPath,
    setSelectedPath,
    onClick,
    cancelClick,
    loading
}: ModalImportCloudConfigJsonProps): JSX.Element {
    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        if (name) {
            setSelectedPath((cuData: any) => ({ ...cuData, [name]: value }));
        }
    };

    const handleCancelClick = () => {
        setSelectedPath('');
        cancelClick();
    };

    useEffect(() => {
        if (path?.length) {
            setSelectedPath({ appId: path });
        }
    }, [path, setSelectedPath]);

    return (
        <div className="w-full h-full p-2 relative">
            <Loading loading={loading} className="" />
            <div className="flex flex-col items-center justify-between gap-4 mb-4">
                <InputField
                    placeholder="App Id"
                    name="appId"
                    formData={selectedPath}
                    onChange={handleChange}
                    spellCheck={false}
                />
                <div className="text-sm font-normal w-full text-error">
                    &bull; If you import a new configuration, all previous data will be destroyed
                    and the new configuration will be applied.
                </div>
            </div>

            <ModalAction
                show
                handleCancelClick={handleCancelClick}
                onClick={onClick}
                disabled={
                    loading || !(typeof selectedPath === 'object' && selectedPath?.appId?.length)
                }
            />
        </div>
    );
}

export default ModalImportCloudConfigJson;
