/* eslint-disable no-underscore-dangle */
import { useApp } from '../../../../context/AppProvider';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import DirectoryModal from '../../../Features/Directory/DirectoryModal';
import LibModal from '../../../Features/LibModal/LibModal';
import { useSource } from '../SourceProvider';
import BackupProcessModal from './BackupProcessModal';
import DestinationLinkingModal from './DestinationLinkingModal';
import ModalController from './ModalController';
import SourceBackupsModal from './SourceBackupsModal';
import SourceFrequencyModal from './SourceFrequencyModal';

function SourceModals() {
    const {
        handelCreateSource,
        handleUpdateSource,
        handleUpdateFrequency,
        selectedPath,
        setSelectedPath,
        loading,
        setForceBackupLoading,
        forceBackupLoading
    } = useSource();

    const { modalType, modalData, closeModal } = useApp();

    const { setConfirm } = useTemplate();

    const modifiedCloseModal = () => {
        closeModal();
        setSelectedPath('');
    };

    const minimizeForceBackupProcess = () => {
        setForceBackupLoading(false);
        setConfirm(null);
    };

    return (
        <>
            {/* modal controller for source operation [Create and Update] */}
            {ModalController(
                modalType,
                modalData,
                closeModal,
                handelCreateSource,
                handleUpdateSource,
                handleUpdateFrequency,
                loading
            )}

            {/* Frequency update modal */}
            {modalType === 'set_frequency' ? (
                <LibModal title="Set Frequency" setOpen={closeModal}>
                    <SourceFrequencyModal
                        data={modalData}
                        onSave={handleUpdateFrequency}
                        closeModal={closeModal}
                        loading={loading}
                    />
                </LibModal>
            ) : null}

            {/* Link destination modal */}
            {modalType === 'link_destination' ? (
                <LibModal title="Link Destination" setOpen={closeModal}>
                    <DestinationLinkingModal
                        data={modalData}
                        onSave={null}
                        closeModal={closeModal}
                        loading={loading}
                    />
                </LibModal>
            ) : null}

            {/* backup process when force backup */}
            {forceBackupLoading && (
                <div className="[&>div]:!z-[1000]">
                    <LibModal>
                        <BackupProcessModal closeClick={minimizeForceBackupProcess} />
                    </LibModal>
                </div>
            )}

            {/* add directory */}
            {modalType === 'directory' ? (
                <LibModal title="Add Directory" className="!w-[400px]" setOpen={modifiedCloseModal}>
                    <DirectoryModal
                        selectedPath={selectedPath}
                        setSelectedPath={setSelectedPath}
                        cancelClick={modifiedCloseModal}
                        onClick={() =>
                            handelCreateSource(null, {
                                type: 'directory',
                                databaseOrPath: selectedPath
                            })
                        }
                        loading={loading}
                    />
                </LibModal>
            ) : null}

            {/* update directory */}
            {modalType === 'update_directory' ? (
                <LibModal
                    title="Update Directory"
                    className="!w-[400px]"
                    setOpen={modifiedCloseModal}
                >
                    <DirectoryModal
                        type="update"
                        path={modalData?.databaseOrPath}
                        selectedPath={selectedPath}
                        setSelectedPath={setSelectedPath}
                        cancelClick={modifiedCloseModal}
                        onClick={() =>
                            handleUpdateSource(null, {
                                _id: modalData?._id,
                                type: 'directory',
                                databaseOrPath: selectedPath
                            })
                        }
                        loading={loading}
                    />
                </LibModal>
            ) : null}

            {/* show backups */}
            {modalType === 'show_backups' ? (
                <LibModal title="Backups" setOpen={modifiedCloseModal}>
                    <SourceBackupsModal data={modalData} />
                </LibModal>
            ) : null}
        </>
    );
}

export default SourceModals;
