/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable react/jsx-props-no-spreading */
import { ChangeEvent, ReactNode, RefObject, useState } from 'react';
import { EyeIcon, EyeIconHide } from './SVGIcon';
import cn from './cn';

export interface OptionType {
    id: string | number;
    title: string;
    value: string | number;
}

export const addOption = (
    id: string | number,
    title: string,
    value: string | number
): OptionType => ({
    id,
    title,
    value
});

// -------- TextareaField --------
interface TextareaFieldProps {
    label?: string;
    name: string;
    onChange: (e: ChangeEvent<HTMLTextAreaElement>) => void;
    formData: Record<string, any>;
    placeholder?: string;
    height?: string;
    className?: string;
    optional?: boolean;
    disabled?: boolean;
    inputRef?: RefObject<HTMLTextAreaElement>;
    required?: boolean;
    [key: string]: any;
}

export function TextareaField({
    label,
    name,
    onChange,
    formData,
    placeholder = '',
    height = 'h-full',
    className = '',
    optional = false,
    disabled = false,
    inputRef,
    required = false,
    ...rest
}: TextareaFieldProps) {
    return (
        <div className={cn('w-full flex flex-col custom-field', className)}>
            {!!label?.length && (
                <label className="pl-1 text-sm md:text-base capitalize h-auto w-full font-normal text-left">
                    {label}
                    {optional && !required && (
                        <span className="text-primary-700 text-sm"> (optional)</span>
                    )}
                    {required && !optional && <span className="text-error text-base">*</span>}
                </label>
            )}
            <textarea
                ref={inputRef}
                name={name}
                value={formData[name] || ''}
                onChange={onChange}
                placeholder={placeholder || label}
                cols={2}
                disabled={disabled}
                autoComplete="off"
                className={cn(
                    `flex-shrink w-full bg-transparent rounded-lg block outline-none border border-primary-200 focus:border-primary px-2 text-primary text-xs md:text-sm leading-6 font-normal disabled:bg-primary-50 disabled:text-primary-700`,
                    height
                )}
                {...rest}
            />
        </div>
    );
}

// -------- SelectOption --------
interface SelectOptionProps {
    label?: string;
    name: string;
    options: OptionType[];
    onChange: (e: ChangeEvent<HTMLSelectElement>) => void;
    formData: Record<string, string | number>;
    placeholder?: string;
    className?: string;
    inputClassName?: string;
    fontWeight?: string;
    optional?: boolean;
    disabled?: boolean;
    required?: boolean;
}

export function SelectOption({
    label,
    name,
    options,
    onChange,
    formData,
    placeholder = '',
    className = '',
    inputClassName = '',
    fontWeight = 'font-normal',
    optional = false,
    disabled = false,
    required = false
}: SelectOptionProps) {
    return (
        <div className={cn('flex flex-col w-full custom-field', className)}>
            {!!label?.length && (
                <label
                    className={`pl-1 mb-1 text-sm md:text-base capitalize w-full text-primary text-left ${fontWeight}`}
                >
                    {label}
                    {optional && !required && (
                        <span className="text-primary-700 text-sm"> (optional)</span>
                    )}
                    {required && !optional && <span className="text-error text-base">*</span>}
                </label>
            )}
            <select
                onChange={onChange}
                name={name}
                value={formData[name] || ''}
                className={cn(
                    `dropdown w-full h-8.5 md:h-10 bg-transparent rounded-lg outline-none border focus:border-primary px-2 text-primary text-xs md:text-sm leading-6 font-normal capitalize disabled:bg-primary-50`,
                    inputClassName
                )}
                disabled={disabled}
            >
                <option value="">Select {placeholder || label}</option>
                {options?.map((option) => (
                    <option key={option.id} value={option.value} className="capitalize">
                        {option.title}
                    </option>
                ))}
            </select>
        </div>
    );
}

// -------- InputCheckbox --------
interface InputCheckboxProps {
    name: string;
    text?: string;
    html?: ReactNode;
    onChange: (e: ChangeEvent<HTMLInputElement>) => void;
    formData: Record<string, any>;
    className?: string;
    disabled?: boolean;
}

export function InputCheckbox({
    name,
    text,
    html,
    onChange,
    formData,
    className = '',
    disabled = false
}: InputCheckboxProps) {
    if (!text?.length && !html) return null;

    return (
        <label
            className={cn(
                'text-base text-primary-700 relative font-medium select-none inline-flex justify-start items-start gap-1.5',
                className
            )}
            htmlFor={name}
        >
            <input
                type="checkbox"
                name={name}
                value={formData[name] ? 'true' : ''}
                onChange={onChange}
                disabled={disabled}
                checked={formData[name] || false}
                id={name}
                className="w-4.5 h-4.5 mt-0.5 appearance-none shrink-0 checked:bg-secondary checked:border-0 rounded-[5px] border border-primary-200 cursor-pointer"
            />
            <svg
                className="absolute cursor-pointer peer-checked:block w-2.5 h-2.5 top-1.5 left-1"
                viewBox="0 0 10 10"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
            >
                <path
                    fillRule="evenodd"
                    clipRule="evenodd"
                    d="M0.972811 6.39607L3.04424 8.82857C3.17388 8.98392 3.34531 9.10035 3.53745 9.16321C3.82138 9.25249 4.21424 9.24642 4.55138 8.76535L9.17817 1.55142C9.21245 1.49821 9.23888 1.43999 9.25638 1.37999C9.32495 1.14428 9.31031 0.62678 8.79674 0.833566C8.65424 0.89178 8.52817 0.985352 8.43138 1.10464L4.07067 6.3525C3.90353 6.55392 3.60495 6.58142 3.40317 6.41428L2.37638 5.25642C2.17924 5.02642 1.89674 4.88678 1.59424 4.86785C1.26745 4.85428 0.889954 4.95035 0.751739 5.42714C0.658882 5.76749 0.741382 6.12999 0.972811 6.39607Z"
                    fill="#F9F9F9"
                />
            </svg>
            <div className="cursor-pointer">
                {text?.length ? (
                    <span className="text-primary font-normal text-sm md:text-base leading-[18px]">
                        {text}
                    </span>
                ) : (
                    html
                )}
            </div>
        </label>
    );
}

// -------- InputPassword --------
interface InputPasswordProps {
    label?: string;
    name: string;
    onChange: (e: ChangeEvent<HTMLInputElement>) => void;
    formData: Record<string, any>;
    placeholder?: string;
    className?: string;
    height?: string;
    optional?: boolean;
    required?: boolean;
    disabled?: boolean;
    eyeIcon?: boolean;
}

export function InputPassword({
    label = '',
    name,
    onChange,
    formData,
    placeholder = '',
    className = '',
    height = '',
    optional = false,
    required = false,
    disabled = false,
    eyeIcon = true
}: InputPasswordProps) {
    const [showPassword, setShowPassword] = useState(false);

    return (
        <div className={cn('w-full flex flex-col custom-field', className)}>
            {!!label?.length && (
                <label className="pl-1 mb-1 text-sm md:text-base capitalize h-auto w-full font-normal">
                    {label}
                    {optional && !required && (
                        <span className="text-primary-700 text-sm"> (optional)</span>
                    )}
                    {required && !optional && <span className="text-error text-base">*</span>}
                </label>
            )}
            <div className="relative h-full w-full custom-field">
                <input
                    type={showPassword ? 'text' : 'password'}
                    name={name}
                    value={formData[name] || ''}
                    onChange={onChange}
                    placeholder={placeholder || label}
                    disabled={disabled}
                    autoComplete="off"
                    className={cn(
                        `flex-shrink w-full h-8.5 md:h-10 bg-transparent rounded-lg block outline-none border border-primary-200 focus:border-primary px-2 text-primary text-xs md:text-sm leading-6 font-normal disabled:bg-primary-50 disabled:text-primary-700`,
                        height
                    )}
                />
                {eyeIcon && (
                    <button
                        type="button"
                        onClick={() => setShowPassword((prev) => !prev)}
                        className="absolute h-4 w-4 top-1/2 right-2 transform -translate-y-1/2 text-primary-700 select-none outline-none focus:outline-none"
                    >
                        {showPassword ? <EyeIcon /> : <EyeIconHide />}
                    </button>
                )}
            </div>
        </div>
    );
}

// -------- InputField (default export) --------
interface InputFieldProps {
    label?: string;
    name: string;
    onChange: (e: ChangeEvent<HTMLInputElement>) => void;
    formData: string | Record<string, any>;
    placeholder?: string;
    className?: string;
    inputClassName?: string;
    optional?: boolean;
    disabled?: boolean;
    readOnly?: boolean;
    type?: string;
    min?: string;
    max?: string;
    required?: boolean;
    [key: string]: any;
}

function InputField({
    label = '',
    name,
    onChange,
    formData,
    placeholder = '',
    className = '',
    inputClassName = '',
    optional = false,
    disabled = false,
    readOnly = false,
    type = 'text',
    min = '',
    max = '',
    required = false,
    ...rest
}: InputFieldProps) {
    return (
        <div className={cn('flex flex-col w-full custom-field', className)}>
            {!!label?.length && (
                <label className="pl-1 mb-1 text-sm md:text-base capitalize w-full font-normal text-left">
                    {label}
                    {optional && !required && (
                        <span className="text-primary-700 text-sm"> (optional)</span>
                    )}
                    {required && !optional && <span className="text-error text-base">*</span>}
                </label>
            )}
            <input
                type={type}
                name={name}
                value={formData[name] || ''}
                min={type === 'number' ? min : undefined}
                max={type === 'number' ? max : undefined}
                onChange={onChange}
                placeholder={placeholder || label}
                disabled={disabled}
                readOnly={readOnly}
                autoComplete="off"
                className={cn(
                    `flex-shrink w-full h-8.5 md:h-10 bg-transparent rounded-lg block outline-none border focus:border-primary border-primary-200 px-2 text-primary text-xs md:text-sm leading-6 font-normal disabled:bg-primary-50 disabled:text-primary-700 read-only:border-primary-100 read-only:bg-primary-50/50 read-only:text-primary-700 read-only:cursor-default`,
                    inputClassName
                )}
                {...rest}
            />
        </div>
    );
}

export default InputField;
