import { useState } from 'react';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { ButtonCancel, ButtonPrimary } from '../../../../utils/Button';
import InputField from '../../../../utils/InputField';

interface ModalNotificationEmailProps {
    data: Record<string, any>;
    closeModal: () => void;
    handleReload: () => void;
}

function ModalNotificationEmail({
    data,
    closeModal,
    handleReload
}: ModalNotificationEmailProps): JSX.Element {
    const [formData, setFormData] = useState<Record<string, any>>({ ...(data || {}) });
    const [loading, setLoading] = useState<boolean>(false);

    const { setMessage } = useTemplate();

    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        if (e) {
            setFormData({ ...formData, [e.target.name]: e.target.value });
        }
    };

    const handleSave = (ev: React.FormEvent<HTMLFormElement>) => {
        ev.preventDefault();
        ev.stopPropagation();
        setLoading(true);
        (window.electronAPI as any)
            ?.saveEmailConfig?.(formData)
            .then(({ error, message }: { error: any; message: any }) => {
                setLoading(false);
                setMessage(message || '');
                if (error === 0) {
                    closeModal();
                    handleReload();
                }
            })
            .catch((err: any) => {
                setLoading(false);
                setMessage(err);
            });
    };
    return (
        <form className="space-y-3" onSubmit={handleSave}>
            <InputField
                name="notificationEmail"
                label="Notification Email"
                formData={formData}
                onChange={onChange}
            />

            <div className="w-full flex justify-end gap-x-2">
                <ButtonCancel title="Cancel" onClick={closeModal} disabled={false}>
                    Cancel
                </ButtonCancel>
                <ButtonPrimary
                    title="Save"
                    btnType="submit"
                    loading={loading}
                    onClick={() => {}}
                    disabled={false}
                />
            </div>
        </form>
    );
}

export default ModalNotificationEmail;
