/* eslint-disable no-underscore-dangle */
import { useEffect, useState } from 'react';
import { useTemplate } from '../../../context/template/TemplateProvider';
import { logsDemoData } from '../../../data/logsDemoData';
import { LogData } from '../../../data/types';
import SectionHeader from '../../Features/SectionHeader/SectionHeader';
import LogItem from './Components/LogItem';
import TitleArea from './Components/TitleArea';

interface ApiResponse {
    error: number;
    message?: string;
    data?: LogData[];
}

function LogSection(): JSX.Element {
    const [logsDataArr, setLogsDataArr] = useState<LogData[]>(logsDemoData);
    const [reloadKey, setReloadKey] = useState<number>(0);
    const { setMessage } = useTemplate();

    const reloadWithData = (): void => {
        setLogsDataArr(logsDemoData);
        setReloadKey((prev) => prev + 1);
    };

    useEffect(() => {
        setLogsDataArr([]);
        window.electronAPI
            ?.getLogFiles()
            .then((res: ApiResponse) => {
                if (res.error === 0) {
                    setLogsDataArr(res?.data || []);
                } else {
                    setLogsDataArr([]);
                    setMessage(res.message || 'Failed to load log files');
                }
            })
            .catch((err: any) => {
                setLogsDataArr([]);
                console.log(err);
                // setMessage(err?.toString());
            });
    }, [reloadKey, setMessage]);
    return (
        <div className="h-full">
            <SectionHeader className="h-21 flex items-center">
                <TitleArea reloadWithData={reloadWithData} />
            </SectionHeader>
            <div className="w-full h-[calc(100%_-_96px)] overflow-auto p-5 pb-24">
                {logsDataArr && logsDataArr.length === 0 && (
                    <div className="flex justify-center items-center h-full">
                        <p className="text-primary-500 text-lg">No logs found!</p>
                    </div>
                )}
                {logsDataArr?.map((backup) => (
                    <LogItem key={backup?.name} data={backup} reloadWithData={reloadWithData} />
                ))}
            </div>
        </div>
    );
}

export default LogSection;
