function ListHeader(): JSX.Element {
    return (
        <div className="flex items-center pb-3">
            <div className="text-xl text-primary font-medium">List of backups</div>
        </div>
    );
}

function BackupList(): JSX.Element {
    const { setConfirm } = useTemplate();

    const handleBackup = (item: BackupData): void => {
        setConfirm({
            text: `Are you sure you want to ${item?.running ? 'stop' : 'start'} this backup?`,
            textAction: 'Yes',
            clickAction: () => {
                setConfirm(null);
            }
        });
    };
    return (
        <div className="w-full h-[calc(100%_-_135px)] py-2.5 px-4">
            <ListHeader />
            <div className="w-full h-[calc(100%_-_51px)] bg-white rounded-2xl overflow-auto ">
                <div>
                    {backupDemoData?.map((backup) => (
                        <BackupListItem key={backup.id} data={backup} handleBackup={handleBackup} />
                    ))}
                </div>
            </div>
        </div>
    );
}

export default BackupList;
