import React, { useEffect, useMemo, useState } from 'react';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { ButtonCancel, ButtonPrimary } from '../../../../utils/Button';
import InputField, { InputCheckbox, InputPassword } from '../../../../utils/InputField';

interface SMTPData {
    hostname?: string;
    port?: string;
    username?: string;
    password?: string;
    default?: boolean;
}

interface FormData {
    hostname: string;
    port: string;
    username: string;
    password: string;
    default: boolean;
}

interface ModalSMTPConfigSetProps {
    closeClick: () => void;
    handleReload: () => void;
    data?: SMTPData;
}

function ModalSMTPConfigSet({
    closeClick,
    handleReload,
    data
}: ModalSMTPConfigSetProps): JSX.Element {
    const [formData, setFormData] = useState<FormData>({
        hostname: data?.hostname || '',
        port: data?.port || '',
        username: data?.username || '',
        password: data?.password || '',
        default: data?.default || true
    });
    const [loading, setLoading] = useState<boolean>(false);
    const { setMessage } = useTemplate();

    const isDefault = formData.default;

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;

        if (name === 'default') {
            setFormData((prev) => ({
                ...prev,
                [name]: !formData.default
            }));
        } else {
            setFormData((prev) => ({
                ...prev,
                [name]: value
            }));
        }
    };

    const handleSave = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        setLoading(true);
        window.electronAPI
            ?.setSMTPConfig(formData)
            .then(({ error, message }) => {
                setLoading(false);
                setMessage(message || '');
                if (!error) {
                    handleReload();
                    closeClick();
                }
            })
            .catch((err: any) => {
                setLoading(false);
                setMessage(err?.toString() || 'An error occurred');
            });
    };

    useEffect(() => {
        if (isDefault) {
            setFormData((prev) => ({
                ...prev,
                hostname: 'smtp.gmail.com',
                port: '587'
            }));
        } else {
            setFormData((prev) => ({
                ...prev,
                hostname: '',
                port: '',
                username: '',
                password: ''
            }));
        }
    }, [isDefault]);

    const inputValue = useMemo(() => {
        return formData.default
            ? {
                  hostname: '---',
                  port: '---',
                  username: '---',
                  password: '********',
                  default: true
              }
            : formData;
    }, [formData]);

    return (
        <form onSubmit={handleSave}>
            <div className="flex flex-col gap-y-2 mb-4">
                <InputField
                    label="Hostname"
                    name="hostname"
                    placeholder="Type Hostname..."
                    formData={inputValue}
                    onChange={handleChange}
                    disabled={isDefault}
                />
                <InputField
                    label="Port"
                    name="port"
                    placeholder="Type Port..."
                    formData={inputValue}
                    onChange={handleChange}
                    disabled={isDefault}
                />
                <InputField
                    label="Username"
                    name="username"
                    placeholder="Type Username..."
                    formData={inputValue}
                    onChange={handleChange}
                    disabled={isDefault}
                />
                <InputPassword
                    label="Password"
                    name="password"
                    placeholder="Type Password..."
                    formData={inputValue}
                    onChange={handleChange}
                    disabled={isDefault}
                    eyeIcon={!isDefault}
                />
                <InputCheckbox
                    text="Default"
                    name="default"
                    html="default"
                    formData={inputValue}
                    onChange={handleChange}
                />
            </div>

            <div className="flex gap-x-2 justify-end">
                <ButtonCancel title="Cancel" onClick={() => closeClick()} disabled={false}>
                    Cancel
                </ButtonCancel>
                <ButtonPrimary
                    title="Save"
                    btnType="submit"
                    loading={loading}
                    onClick={() => {}}
                    disabled={false}
                />
            </div>
        </form>
    );
}

export default ModalSMTPConfigSet;
