import { useEffect } from 'react';
import { FileIcon } from '../../utils/SVGIcon';
import exploreDirectory from '../../utils/exploreDirectory';

// Define types for message object
interface MessageType {
    text?: string;
    timeout?: number;
    directory?: string;
    [key: string]: any;
}

interface MessageTemplateProps {
    message: MessageType | string | null;
    setMessage: (message: MessageType | string | null) => void;
}

function CrossIcon(): JSX.Element {
    return (
        <svg
            aria-hidden="true"
            focusable="false"
            data-prefix="fal"
            data-icon="times"
            role="img"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 320 512"
        >
            <path
                fill="currentColor"
                d="M193.94 256L296.5 153.44l21.15-21.15c3.12-3.12 3.12-8.19 0-11.31l-22.63-22.63c-3.12-3.12-8.19-3.12-11.31 0L160 222.06 36.29 98.34c-3.12-3.12-8.19-3.12-11.31 0L2.34 120.97c-3.12 3.12-3.12 8.19 0 11.31L126.06 256 2.34 379.71c-3.12 3.12-3.12 8.19 0 11.31l22.63 22.63c3.12 3.12 8.19 3.12 11.31 0L160 289.94 262.56 392.5l21.15 21.15c3.12 3.12 8.19 3.12 11.31 0l22.63-22.63c3.12-3.12 3.12-8.19 0-11.31L193.94 256z"
            />
        </svg>
    );
}
function MessageTemplate({ message, setMessage }: MessageTemplateProps) {
    const handleClose = (): void => {
        setMessage(null);
    };

    useEffect(() => {
        const timeout = typeof message === 'object' && message ? message.timeout : 6000;
        const timer = setTimeout(() => {
            setMessage(null);
        }, timeout || 6000);

        return () => {
            clearTimeout(timer);
        };
    }, [message, setMessage]);

    if (!message) {
        return null;
    }

    console.log(message);

    const messageObj = typeof message === 'string' ? { text: message } : message;

    return (
        <div className="floating-message flex flex-nowrap">
            <span className="flex-auto self-center">
                {typeof message === 'string' ? message : messageObj?.text || 'Message'}
            </span>
            {messageObj.directory ? (
                <button
                    type="button"
                    data-tooltip-id="myToolTip"
                    data-tooltip-content="Explore Directory"
                    className="mr-4"
                    onClick={() => exploreDirectory(messageObj.directory!)}
                >
                    <FileIcon className="w-7 [&>path]:fill-white" />
                </button>
            ) : null}
            <button type="button" onClick={handleClose} className="flex-initial self-center">
                <CrossIcon />
            </button>
        </div>
    );
}

export default MessageTemplate;
