import { useState } from 'react';
import { useApp } from '../../../../context/AppProvider';
import { useTemplate } from '../../../../context/template/TemplateProvider';
import { ButtonPrimary } from '../../../../utils/Button';
import InstOption from '../../../../utils/InstOption';
import { ReloadIcon } from '../../../../utils/SVGIcon';
import { useConfig } from '../../Config/ConfigProvider';

function TitleArea({ handleReload }) {
    const [showMenu, setShowMenu] = useState(false);
    const { openModal, navigate } = useApp();

    const { defaultDir, handleReload: reload } = useConfig();

    const { setConfirm } = useTemplate();

    // const handleSavePath = (type, fileDirectory, modal) => {
    //     setDumpPathModal({ mode: false, data: '' });
    //     handleSetDumpPath(type, fileDirectory, (modal = 'true'));
    // };

    const handleAddSource = () => {
        if (defaultDir) {
            setShowMenu(true);
        } else {
            setConfirm({
                text: `Default directory is not set. Please set default directory from configurations.`,
                buttons: ['Ok'],
                clickAction: () => {
                    console.log('config');
                    setConfirm(null);
                    navigate('config');
                    openModal('default_dir');
                }
            });
        }
    };

    const handleOptionClick = (ev, type) => {
        ev.preventDefault();
        ev.stopPropagation();
        reload();
        openModal(type);
    };

    return (
        <div className="h-full flex items-center justify-between">
            <div className="">
                <p className="text-[25px] text-primary font-medium leading-[30px]">Sources:</p>
            </div>

            <div className="flex items-center gap-x-2">
                <button type="button" className="block h-10" onClick={handleReload}>
                    <ReloadIcon />
                </button>
                <div className="relative">
                    <ButtonPrimary title="Add+" onClick={handleAddSource} />

                    <InstOption
                        showMenu={showMenu}
                        setShowMenu={setShowMenu}
                        className="top-[57px] right-1 w-48"
                    >
                        {/* <button type="button" onClick={() => openModal('mysql-win')}>
                            MySQL
                        </button> */}

                        <button type="button" onClick={(ev) => handleOptionClick(ev, 'mssql-win')}>
                            MsSQL(Win Auth)
                        </button>
                        <button type="button" onClick={(ev) => handleOptionClick(ev, 'mssql-host')}>
                            MsSQL(Password Auth)
                        </button>
                        <button type="button" onClick={(ev) => handleOptionClick(ev, 'mysql')}>
                            MySQL
                        </button>
                        <button type="button" onClick={(ev) => handleOptionClick(ev, 'pgsql')}>
                            Postgres SQL
                        </button>
                        <button type="button" onClick={(ev) => handleOptionClick(ev, 'directory')}>
                            Directory
                        </button>
                    </InstOption>
                </div>
            </div>

            {/* {dumpPathModal.mode ? (
                <div className="[&>.lib-modal]:z-[10000]">
                    <LibModal
                        title="Set Dump Path"
                        className="!w-[400px]"
                        setOpen={() => setDumpPathModal({ mode: false, data: '' })}
                    >
                        <ModalDumpPathConfig
                            data={configsData}
                            type={dumpPathModal.type}
                            closeModal={() => setDumpPathModal({ mode: false, data: '' })}
                            handleSetDumpPath={handleSavePath}
                            loadingOnSave={loading}
                        />
                    </LibModal>
                </div>
            ) : null} */}
        </div>
    );
}

export default TitleArea;
