import React, { createContext, ReactNode, useContext, useMemo, useState } from 'react';
import { menuList } from '../app/Sidebar/menuList';
import '../app/types/electron'; // Import electron types

// Define types for menu item
export interface MenuItem {
    id: number;
    title: string;
    icon: React.ReactElement;
    section: string;
    redirect?: boolean;
    redirectUrl?: string;
    prev?: string | null;
}

// Define types for modal state
interface ModalState {
    type: string;
    data: any;
}

// Define the context type
interface AppContextType {
    openModal: (type: string, data?: any) => void;
    closeModal: () => void;
    modalType: string;
    modalData: any;
    handleSelectMenu: (menu: MenuItem) => void;
    selectedMenu: MenuItem;
    navigate: (sectionName: string, data?: any) => void;
    navigateData: any;
}

const AppContext = createContext<AppContextType | undefined>(undefined);

export const useApp = (): AppContextType => {
    const context = useContext(AppContext);
    if (context === undefined) {
        throw new Error('useApp must be used within an AppProvider');
    }
    return context;
};

interface AppProviderProps {
    children: ReactNode;
}

function AppProvider({ children }: AppProviderProps) {
    // --State to store selected menu
    const [selectedMenu, setSelectedMenu] = useState<MenuItem>(menuList[0]);

    // --State to store navigate data
    const [navigateData, setNavigateData] = useState<any>(null);

    // --Modal open handler
    const [showModal, setShowModal] = useState<ModalState>({
        type: '',
        data: null
    });

    const value = useMemo((): AppContextType => {
        // --Handler to pass data and navigate to another section
        const navigate = (sectionName: string, data: any = null): void => {
            // --Set data to when navigate if data is available
            setNavigateData(data);

            // --Find the menu from menuList by section name
            const menu = menuList.find((m) => m.section === sectionName) || {
                id: 0,
                title: sectionName,
                icon: <></>,
                section: sectionName
            };

            // --Set the selected menu
            setSelectedMenu({
                ...menu,
                prev: selectedMenu?.section || null
            });
        };

        const handleSelectMenu = (menu: MenuItem): void => {
            if (menu.redirect) {
                window.electronAPI?.openLink(menu.redirectUrl || '');
                return;
            }
            navigate('', null);
            setSelectedMenu(menu);
        };

        // --Handler to open modal
        const openModal = (type: string, data?: any): void => {
            setShowModal({
                type,
                data
            });
        };
        // --Handler to close modal
        const closeModal = (): void => {
            setShowModal({
                type: '',
                data: null
            });
        };

        return {
            openModal,
            closeModal,
            modalType: showModal?.type,
            modalData: showModal?.data,
            handleSelectMenu,
            selectedMenu,

            navigate,
            navigateData
        };
    }, [navigateData, selectedMenu, showModal?.data, showModal?.type]);
    return <AppContext.Provider value={value}>{children}</AppContext.Provider>;
}

export default AppProvider;
